// Get contacts for a specific list
export const getContactsForList = async (listId: string, page?: number, search?: string) => {
  try {
    const response = await axiosInstance.get(`/api/lists/${listId}/contacts`, {
      params: { page, search },
    });
    return response.data;
  } catch (error: any) {
    console.error("Error fetching list contacts:", error);

    // Check if it's a "no contacts found" scenario (404 or specific error message)
    const isNoContactsFound =
      error.response?.status === 404 ||
      error.response?.data?.message?.toLowerCase().includes('not found') ||
      error.response?.data?.error?.toLowerCase().includes('not found');

    // Only show toast for actual errors, not when no contacts are found
    if (!isNoContactsFound) {
      const errorMessage = error.response?.data?.error || error.response?.data?.message || "Failed to fetch list contacts";
      toast.error(errorMessage);
    }

    throw error;
  }
};

