// Add contact to list
export const addContactToList = async (listId: string, contact: ContactPayload) => {
  try {
    // Map frontend field names to backend expected names
    const payload = {
      email: contact.email,
      contact_number: contact.phoneNumber || contact.contact_number, // Map phoneNumber to contact_number
      first_name: contact.fullName || contact.firstName || '', // Map fullName to first_name
    };

    const response = await axiosInstance.post(`/api/lists/${listId}/contacts`, payload);
    // Don't show toast here - let the component handle it
    return response.data;
  } catch (error: any) {
    console.error("Error adding contact to list:", error);
    const message = error.response?.data?.error || error.response?.data?.message || "Failed to add contact to list";
    toast.error(message);
    throw error;
  }
};

