import { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { toast } from 'sonner';
import secureLocalStorage from 'react-secure-storage';
import axiosInstance from '../api/axios/axiosInstance';
import { useAuth } from '../context/AuthContext';
import { Button } from '../components/ui/button';
import { Badge } from '../components/ui/badge';
import { Input } from '../components/ui/input';
import {
  ArrowLeftIcon,
  PencilIcon,
  PhoneIcon,
  EnvelopeIcon,
  CalendarIcon,
  BuildingOfficeIcon,
  TagIcon,
  MapPinIcon,
  GlobeAltIcon,
  StarIcon,
  ClockIcon,
  DevicePhoneMobileIcon,
  PaperAirplaneIcon,
  EllipsisVerticalIcon,
  UserIcon,
  CheckIcon,
  XMarkIcon
} from '@heroicons/react/24/outline';

const LeadDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [lead, setLead] = useState(null);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('sms'); // Changed default to 'sms'
  const [isEditing, setIsEditing] = useState(false);
  const [editedLead, setEditedLead] = useState({});
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [communicationType, setCommunicationType] = useState('sms'); // 'sms', 'email' - removed 'conversation'
  const [sendingSMS, setSendingSMS] = useState(false);

  // Tags state
  const [tags, setTags] = useState([]);
  const [tagsLoading, setTagsLoading] = useState(false);
  const [leadTags, setLeadTags] = useState([]);
  const [selectedTags, setSelectedTags] = useState([]);

  // Email state
  const [emailSubject, setEmailSubject] = useState('');
  const [emailBody, setEmailBody] = useState('');
  const [sendingEmail, setSendingEmail] = useState(false);
  const [connectedGmail, setConnectedGmail] = useState(null);
  const [gmailLoading, setGmailLoading] = useState(true);

  // Status options
  const statusOptions = [
    { value: 'new', label: 'New', color: 'bg-blue-100 text-blue-800' },
    { value: 'contacted', label: 'Contacted', color: 'bg-yellow-100 text-yellow-800' },
    { value: 'qualified', label: 'Qualified', color: 'bg-green-100 text-green-800' },
    { value: 'proposal', label: 'Proposal', color: 'bg-purple-100 text-purple-800' },
    { value: 'closed', label: 'Closed', color: 'bg-gray-100 text-gray-800' },
    { value: 'lost', label: 'Lost', color: 'bg-red-100 text-red-800' }
  ];

  // Load lead data
  const loadLead = async () => {
    try {
      const token = secureLocalStorage.getItem('token');
      if (!token) {
        navigate('/login');
        return;
      }

      const response = await fetch(`${import.meta.env.VITE_APP_BASE_URL}/api/leads-contacts/getleadsbyid/${id}`, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });

      const data = await response.json();
      if (data.success) {
        setLead(data.lead);
        setEditedLead(data.lead);
        // Set lead tags and selected tags
        const leadTagsData = data.lead.tags || [];
        setLeadTags(leadTagsData);
        setSelectedTags(leadTagsData.map(tag => tag.tag_id));
      } else {
        toast.error(data.message || 'Failed to load lead');
        navigate('/leads-contacts');
      }
    } catch (error) {
      console.error('Load lead error:', error);
      toast.error('Failed to load lead details');
      navigate('/leads-contacts');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadLead();
    fetchGmailStatus();
  }, [id]);

  // Fetch Gmail connection status
  const fetchGmailStatus = async () => {
    try {
      setGmailLoading(true);
      const response = await axiosInstance.get('/api/integration-gmail/auth/status');
      if (response.data.success && response.data.data.isConnected) {
        setConnectedGmail(response.data.data.profile?.email || null);
      } else {
        setConnectedGmail(null);
      }
    } catch (error) {
      console.error('Error fetching Gmail status:', error);
      setConnectedGmail(null);
    } finally {
      setGmailLoading(false);
    }
  };

  // Fetch tags when editing mode is enabled
  useEffect(() => {
    if (isEditing) {
      fetchTags();
    }
  }, [isEditing]);

  // Fetch user's tags
  const fetchTags = async () => {
    try {
      setTagsLoading(true);

      console.log(' Frontend: User from AuthContext:', user);

      if (!user || !user.id) {
        console.log(' Frontend: No user found in AuthContext');
        return;
      }

      console.log(' Frontend: Making API call to get tags for user ID:', user.id);
      const response = await axiosInstance.get(`/api/tags/get-tags-by-user/${user.id}`);

      console.log(' Frontend: API response:', response.data);

      if (response.data.success) {
        console.log(' Frontend: Setting tags:', response.data.data);
        setTags(response.data.data);
      } else {
        console.log(' Frontend: API returned success=false:', response.data.message);
      }
    } catch (error) {
      console.error(' Frontend: Error fetching tags:', error);
      console.log(' Frontend: Error details:', error.response?.data);
      toast.error('Failed to load tags');
    } finally {
      setTagsLoading(false);
    }
  };

  // Handle tag selection (multiple tags like GHL)
  const handleTagToggle = (tagId) => {
    setSelectedTags(prev => {
      if (prev.includes(tagId)) {
        return prev.filter(id => id !== tagId);
      } else {
        return [...prev, tagId];
      }
    });
  };

  // Save tags to lead
  const handleSaveTags = async () => {
    try {
      const response = await axiosInstance.post(`/api/leads-contacts/${id}/tags`, {
        tag_ids: selectedTags
      });

      if (response.data.success) {
        // Update lead tags display
        const updatedTags = tags.filter(tag => selectedTags.includes(tag.tag_id));
        setLeadTags(updatedTags);
        toast.success('Tags updated successfully');
      }
    } catch (error) {
      console.error('Error updating tags:', error);
      toast.error('Failed to update tags');
    }
  };

  // Remove tag from lead
  const handleRemoveTag = async (tagId) => {
    try {
      const response = await axiosInstance.delete(`/api/leads-contacts/${id}/tags/${tagId}`);

      if (response.data.success) {
        setLeadTags(prev => prev.filter(tag => tag.tag_id !== tagId));
        setSelectedTags(prev => prev.filter(id => id !== tagId));
        toast.success('Tag removed successfully');
      }
    } catch (error) {
      console.error('Error removing tag:', error);
      toast.error('Failed to remove tag');
    }
  };

  // Helper functions
  const formatDate = (dateString) => {
    if (!dateString) return 'N/A';
    const date = new Date(dateString);
    return date.toLocaleString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getStatusColor = (status) => {
    const statusOption = statusOptions.find(option => option.value === status);
    return statusOption ? statusOption.color : 'bg-gray-100 text-gray-800';
  };

  const getInitials = (name) => {
    if (!name) return 'L';
    return name.split(' ').map(word => word[0]).join('').toUpperCase().slice(0, 2);
  };

  const getAvatarColor = (name) => {
    if (!name) return 'bg-blue-500';
    const colors = [
      'bg-blue-500', 'bg-green-500', 'bg-purple-500', 'bg-pink-500',
      'bg-indigo-500', 'bg-yellow-500', 'bg-red-500', 'bg-teal-500'
    ];
    const index = name.charCodeAt(0) % colors.length;
    return colors[index];
  };

  // Edit handlers
  const handleEdit = () => {
    setIsEditing(true);
  };

  const handleSave = async () => {
    try {
      const token = secureLocalStorage.getItem('token');
      const response = await fetch(`${import.meta.env.VITE_APP_BASE_URL}/api/leads-contacts/${id}`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(editedLead)
      });

      const data = await response.json();
      if (data.success) {
        setLead(editedLead);
        setIsEditing(false);
        toast.success('Lead updated successfully');
      } else {
        toast.error(data.message || 'Failed to update lead');
      }
    } catch (error) {
      console.error('Update lead error:', error);
      toast.error('Failed to update lead');
    }
  };

  const handleCancel = () => {
    setEditedLead(lead);
    setIsEditing(false);
  };

  const handleInputChange = (field, value) => {
    setEditedLead(prev => ({
      ...prev,
      [field]: value
    }));
  };

  // Contact handlers
  const handleCall = () => {
    if (lead?.phone) {
      window.open(`tel:${lead.phone}`, '_self');
    }
  };

  const handleEmail = () => {
    if (lead?.email) {
      setCommunicationType('email');
      setActiveTab('conversation');
    }
  };

  const handleSMS = () => {
    if (lead?.phone) {
      setCommunicationType('sms');
      setActiveTab('conversation');
    }
  };

  const handleWebsite = () => {
    if (lead?.website) {
      window.open(lead.website.startsWith('http') ? lead.website : `https://${lead.website}`, '_blank');
    }
  };

  // Message handlers
  const handleSendMessage = async () => {
    if (!newMessage.trim()) return;

    if (communicationType === 'sms') {
      // Send SMS via backend API
      try {
        setSendingSMS(true);
        const token = secureLocalStorage.getItem('token');

        const response = await fetch(`${import.meta.env.VITE_APP_BASE_URL}/api/leads-contacts/${id}/sms`, {
          method: 'POST',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ message: newMessage.trim() })
        });

        const data = await response.json();

        if (data.success) {
          const messageData = {
            id: Date.now(),
            text: newMessage,
            type: 'sms',
            timestamp: new Date(),
            sent: true,
            status: 'sent'
          };
          setMessages(prev => [...prev, messageData]);
          setNewMessage('');
          toast.success('SMS sent successfully');
        } else {
          toast.error(data.message || 'Failed to send SMS');
        }
      } catch (error) {
        console.error('Send SMS error:', error);
        toast.error('Failed to send SMS');
      } finally {
        setSendingSMS(false);
      }
    } else {
      // For email or other types, just add to local messages
      const messageData = {
        id: Date.now(),
        text: newMessage,
        type: communicationType,
        timestamp: new Date(),
        sent: true
      };

      setMessages(prev => [...prev, messageData]);
      setNewMessage('');
      toast.success(`${communicationType.toUpperCase()} sent successfully`);
    }
  };

  // Send email via Gmail Integration
  const handleSendEmail = async () => {
    if (!emailSubject.trim()) {
      toast.error('Please enter email subject');
      return;
    }
    if (!emailBody.trim()) {
      toast.error('Please enter email message');
      return;
    }
    if (!connectedGmail) {
      toast.error('Please connect your Gmail account in Integrations first');
      return;
    }
    if (!lead?.email) {
      toast.error('This lead does not have an email address');
      return;
    }

    try {
      setSendingEmail(true);
      const response = await axiosInstance.post('/api/integration-gmail/send', {
        to: lead.email,
        subject: emailSubject.trim(),
        body: emailBody.trim()
      });

      if (response.data.success) {
        // Add to messages
        const messageData = {
          id: Date.now(),
          text: `Subject: ${emailSubject}\n\n${emailBody}`,
          type: 'email',
          timestamp: new Date(),
          sent: true,
          from: connectedGmail,
          to: lead.email
        };
        setMessages(prev => [...prev, messageData]);
        setEmailSubject('');
        setEmailBody('');
        toast.success('Email sent successfully!');
      } else {
        toast.error(response.data.message || 'Failed to send email');
      }
    } catch (error) {
      console.error('Send email error:', error);
      toast.error(error.response?.data?.message || 'Failed to send email');
    } finally {
      setSendingEmail(false);
    }
  };

  // Update status
  const updateStatus = async (newStatus) => {
    try {
      const token = secureLocalStorage.getItem('token');
      const response = await fetch(`${import.meta.env.VITE_APP_BASE_URL}/api/leads-contacts/${id}/status`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ status: newStatus })
      });

      const data = await response.json();
      if (data.success) {
        setLead(prev => ({ ...prev, status: newStatus }));
        toast.success('Status updated successfully');
      } else {
        toast.error(data.message || 'Failed to update status');
      }
    } catch (error) {
      console.error('Update status error:', error);
      toast.error('Failed to update status');
    }
  };

  if (loading) {
    return (
      <div className="h-screen bg-gray-50 flex items-center justify-center">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  if (!lead) {
    return (
      <div className="h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-xl font-semibold text-gray-900 mb-2">Lead not found</h2>
          <Button onClick={() => navigate('/leads-contacts')}>
            Back to Leads
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className="max-h-screen bg-gray-50 flex flex-col overflow-hidden">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 px-6 py-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <Button
              variant="ghost"
              size="sm"
              onClick={() => navigate('/leads-contacts')}
              className="flex items-center space-x-2"
            >
              <ArrowLeftIcon className="h-4 w-4" />
              <span>Back to Leads</span>
            </Button>
            <div className="h-6 w-px bg-gray-300"></div>
            <div className="flex items-center space-x-3">
              <div className={`w-8 h-8 rounded-full flex items-center justify-center text-white text-sm font-medium ${getAvatarColor(lead?.business_name)}`}>
                {getInitials(lead?.business_name)}
              </div>
              <h1 className="text-xl font-bold text-gray-900">
                {lead?.business_name || 'Lead Details'}
              </h1>
              <Badge className={`${getStatusColor(lead?.status)} px-2 py-1 text-xs font-medium`}>
                {lead?.status?.replace('_', ' ').toUpperCase() || 'NEW'}
              </Badge>
            </div>
          </div>
          <div className="flex items-center space-x-2">
            {isEditing ? (
              <>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleCancel}
                  className="flex items-center space-x-2"
                >
                  <XMarkIcon className="h-4 w-4" />
                  <span>Cancel</span>
                </Button>
                <Button
                  size="sm"
                  onClick={handleSave}
                  className="flex items-center space-x-2 bg-blue-600 hover:bg-blue-700"
                >
                  <CheckIcon className="h-4 w-4" />
                  <span>Save</span>
                </Button>
              </>
            ) : (
              <Button
                variant="outline"
                size="sm"
                onClick={handleEdit}
                className="flex items-center space-x-2"
              >
                <PencilIcon className="h-4 w-4" />
                <span>Edit</span>
              </Button>
            )}
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex flex-1 min-h-0">
        {/* Left Panel - Lead Information */}
        <div className="w-80 bg-white border-r border-gray-200 flex flex-col min-h-0">
          {/* Contact Actions */}
          <div className="p-4 border-b border-gray-200">
            <div className="grid grid-cols-2 gap-2">
              <Button
                variant="outline"
                size="sm"
                onClick={handleCall}
                disabled={!lead?.phone}
                className="flex items-center justify-center space-x-2 h-10"
              >
                <PhoneIcon className="h-4 w-4 stroke-2" />
                <span>Call</span>
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={handleSMS}
                disabled={!lead?.phone}
                className="flex items-center justify-center space-x-2 h-10"
              >
                <DevicePhoneMobileIcon className="h-4 w-4 stroke-2" />
                <span>SMS</span>
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={handleEmail}
                disabled={!lead?.email}
                className="flex items-center justify-center space-x-2 h-10"
              >
                <EnvelopeIcon className="h-4 w-4 stroke-2 " />
                <span>Email</span>
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={handleWebsite}
                disabled={!lead?.website}
                className="flex items-center justify-center space-x-2 h-10"
              >
                <GlobeAltIcon className="h-4 w-4 stroke-2" />
                <span>Website</span>
              </Button>
            </div>
          </div>

          {/* Lead Information */}
          <div className="flex-1 p-4">
            <div className="space-y-4">
              {/* Tags Section - Multiple Selection like GHL */}
              {isEditing && (
                <div className="border border-blue-200 rounded-lg p-3 bg-blue-50">
                  <div className="flex items-center justify-between mb-3">
                    <h3 className="text-sm font-medium text-gray-900">Tags</h3>
                    <button
                      onClick={handleSaveTags}
                      className="text-xs bg-blue-600 text-white px-2 py-1 rounded hover:bg-blue-700"
                    >
                      Save Tags
                    </button>
                  </div>
                  <div className="space-y-2">
                    {tagsLoading ? (
                      <div className="flex items-center justify-center py-2">
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600"></div>
                        <span className="ml-2 text-sm text-gray-500">Loading tags...</span>
                      </div>
                    ) : (
                      <div className="space-y-2 max-h-40 overflow-y-auto">
                        {tags.map((tag) => (
                          <label key={tag.tag_id} className="flex items-center space-x-2 cursor-pointer hover:bg-gray-50 p-1 rounded">
                            <input
                              type="checkbox"
                              checked={selectedTags.includes(tag.tag_id)}
                              onChange={() => handleTagToggle(tag.tag_id)}
                              className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                            />
                            <span className="text-sm text-gray-900">{tag.tag_name}</span>
                          </label>
                        ))}
                      </div>
                    )}
                    {tags.length === 0 && !tagsLoading && (
                      <div className="text-xs text-gray-500 mt-1 p-2 bg-yellow-50 border border-yellow-200 rounded">
                        <p className="font-medium">No tags available.</p>
                        <p>Go to Settings  Tags to create tags first, then come back here.</p>
                      </div>
                    )}
                    {selectedTags.length > 0 && (
                      <div className="mt-2 pt-2 border-t border-gray-200">
                        <p className="text-xs text-gray-500 mb-1">Selected tags:</p>
                        <div className="flex flex-wrap gap-1">
                          {selectedTags.map((tagId) => {
                            const tag = tags.find(t => t.tag_id === tagId);
                            return tag ? (
                              <Badge key={tagId} variant="secondary" className="text-xs">
                                {tag.tag_name}
                              </Badge>
                            ) : null;
                          })}
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              )}

              {/* Current Tags Display */}
              {!isEditing && leadTags.length > 0 && (
                <div>
                  <h3 className="text-sm font-medium text-gray-900 mb-3">Tags</h3>
                  <div className="flex flex-wrap gap-2">
                    {leadTags.map((tag) => (
                      <div key={tag.tag_id} className="flex items-center space-x-1">
                        <Badge variant="secondary" className="text-sm flex items-center space-x-1">
                          <TagIcon className="h-3 w-3" />
                          <span>{tag.tag_name}</span>
                          <button
                            onClick={() => handleRemoveTag(tag.tag_id)}
                            className="ml-1 hover:bg-gray-300 rounded-full p-0.5"
                          >
                            <XMarkIcon className="h-3 w-3" />
                          </button>
                        </Badge>
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {/* Basic Information */}
              <div>

                <div className="space-y-3">
                  {/* Business Name */}
                  <div>
                    <label className="block text-xs font-medium text-gray-500 mb-1">Business Name</label>
                    {isEditing ? (
                      <Input
                        value={editedLead.business_name || ''}
                        onChange={(e) => handleInputChange('business_name', e.target.value)}
                        className="text-sm"
                      />
                    ) : (
                      <div className="text-sm text-gray-900">{lead.business_name || 'N/A'}</div>
                    )}
                  </div>

                  {/* Phone */}
                  {(lead.phone || isEditing) && (
                    <div>
                      <label className="block text-xs font-medium text-gray-500 mb-1">Phone</label>
                      {isEditing ? (
                        <Input
                          value={editedLead.phone || ''}
                          onChange={(e) => handleInputChange('phone', e.target.value)}
                          className="text-sm"
                        />
                      ) : (
                        <div className="text-sm text-gray-900">{lead.phone || 'N/A'}</div>
                      )}
                    </div>
                  )}

                  {/* Email */}
                  {(lead.email || isEditing) && (
                    <div>
                      <label className="block text-xs font-medium text-gray-500 mb-1">Email</label>
                      {isEditing ? (
                        <Input
                          value={editedLead.email || ''}
                          onChange={(e) => handleInputChange('email', e.target.value)}
                          className="text-sm"
                        />
                      ) : (
                        <div className="text-sm text-gray-900">{lead.email || 'N/A'}</div>
                      )}
                    </div>
                  )}

                  {/* Website */}
                  {(lead.website || isEditing) && (
                    <div>
                      <label className="block text-xs font-medium text-gray-500 mb-1">Website</label>
                      {isEditing ? (
                        <Input
                          value={editedLead.website || ''}
                          onChange={(e) => handleInputChange('website', e.target.value)}
                          className="text-sm"
                        />
                      ) : (
                        <div className="text-sm text-gray-900">{lead.website || 'N/A'}</div>
                      )}
                    </div>
                  )}

                  {/* Address */}
                  {(lead.address || isEditing) && (
                    <div>
                      <label className="block text-xs font-medium text-gray-500 mb-1">Address</label>
                      {isEditing ? (
                        <Input
                          value={editedLead.address || ''}
                          onChange={(e) => handleInputChange('address', e.target.value)}
                          className="text-sm"
                        />
                      ) : (
                        <div className="text-sm text-gray-900">{lead.address || 'N/A'}</div>
                      )}
                    </div>
                  )}
                </div>
              </div>

              {/* Business Details */}
              {(lead.business_type || lead.rating || lead.total_ratings || isEditing) && (
                <div>
                  <h3 className="text-sm font-medium text-gray-900 mb-3">Business Details</h3>
                  <div className="space-y-3">
                    {/* Business Type */}
                    {(lead.business_type || isEditing) && (
                      <div>
                        <label className="block text-xs font-medium text-gray-500 mb-1">Business Type</label>
                        {isEditing ? (
                          <Input
                            value={editedLead.business_type || ''}
                            onChange={(e) => handleInputChange('business_type', e.target.value)}
                            className="text-sm"
                          />
                        ) : (
                          <div className="text-sm text-gray-900">{lead.business_type || 'N/A'}</div>
                        )}
                      </div>
                    )}

                    {/* Rating */}
                    {(lead.rating || isEditing) && (
                      <div>
                        <label className="block text-xs font-medium text-gray-500 mb-1">Rating</label>
                        <div className="text-sm text-gray-900">
                          {lead.rating ? `${lead.rating}/5` : 'N/A'}
                          {lead.total_ratings && ` (${lead.total_ratings} reviews)`}
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              )}

              {/* Lead Source */}
              {(lead.search_query || lead.search_location || isEditing) && (
                <div>
                  <h3 className="text-sm font-medium text-gray-900 mb-3">Lead Source</h3>
                  <div className="space-y-3">
                    {(lead.search_query || isEditing) && (
                      <div>
                        <label className="block text-xs font-medium text-gray-500 mb-1">Search Query</label>
                        <div className="text-sm text-gray-900">{lead.search_query || 'N/A'}</div>
                      </div>
                    )}
                    {(lead.search_location || isEditing) && (
                      <div>
                        <label className="block text-xs font-medium text-gray-500 mb-1">Search Location</label>
                        <div className="text-sm text-gray-900">{lead.search_location || 'N/A'}</div>
                      </div>
                    )}
                  </div>
                </div>
              )}

              {/* Timestamps */}
              <div>
                <h3 className="text-sm font-medium text-gray-900 mb-3">Timeline</h3>
                <div className="space-y-3">
                  <div>
                    <label className="block text-xs font-medium text-gray-500 mb-1">Created</label>
                    <div className="text-sm text-gray-900">{formatDate(lead.created_at)}</div>
                  </div>
                  {lead.updated_at && lead.updated_at !== lead.created_at && (
                    <div>
                      <label className="block text-xs font-medium text-gray-500 mb-1">Last Updated</label>
                      <div className="text-sm text-gray-900">{formatDate(lead.updated_at)}</div>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Right Panel - SMS/Email & Actions */}
        <div className="flex-1 bg-white flex  flex-col min-h-0">
          {/* Tab Navigation - SMS and Email tabs directly */}
          <div className="border-b border-gray-200 px-6  py-3">
            <div className="flex space-x-6">
              <button
                onClick={() => { setActiveTab('sms'); setCommunicationType('sms'); }}
                className={`pb-2 text-sm font-medium border-b-2 transition-colors ${
                  activeTab === 'sms'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700'
                }`}
              >
                <div className="flex items-center space-x-2">
                  <DevicePhoneMobileIcon className="h-4 w-4 stroke-2"  />
                  <span>SMS</span>
                </div>
              </button>
              <button
                onClick={() => { setActiveTab('email'); setCommunicationType('email'); }}
                className={`pb-2 text-sm font-medium border-b-2 transition-colors ${
                  activeTab === 'email'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700'
                }`}
              >
                <div className="flex items-center space-x-2">
                  <EnvelopeIcon className="h-4 w-4 stroke-2" />
                  <span>Email</span>
                </div>
              </button>
              <button
                onClick={() => setActiveTab('activity')}
                className={`pb-2 text-sm font-medium border-b-2 transition-colors ${
                  activeTab === 'activity'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700'
                }`}
              >
                <div className="flex items-center space-x-2">
                  <ClockIcon className="h-4 w-4 stroke-2" />
                  <span>Activity</span>
                </div>
              </button>
            </div>
          </div>

          {/* Tab Content */}
          <div className="flex-1 flex flex-col min-h-0 overflow-y-auto">
            {activeTab === 'sms' ? (
              <>
                {/* SMS Interface */}
                <div className="flex-1 p-6 ">
                  <div className="bg-gray-50 rounded-lg p-4 mb-4">
                    <div className="flex items-center space-x-2 mb-2">
                      <DevicePhoneMobileIcon className="h-5 w-5 text-blue-600" />
                      <span className="font-medium text-gray-900">SMS to {lead?.phone}</span>
                    </div>
                    <p className="text-sm text-gray-600">Send SMS message to this lead via Twilio</p>
                    <p className="text-xs text-gray-400 mt-1">From: {import.meta.env.VITE_TWILIO_PHONE_NUMBER || '+13465940138'}</p>
                  </div>

                  {/* SMS Messages */}
                  <div className="space-y-3">
                    {messages.filter(m => m.type === 'sms').length === 0 ? (
                      <div className="text-center text-gray-500 mt-8">
                        <DevicePhoneMobileIcon className="h-12 w-12 mx-auto mb-4 text-gray-300" />
                        <p>No SMS messages yet. Send your first message!</p>
                      </div>
                    ) : (
                      messages.filter(m => m.type === 'sms').map((message) => (
                        <div
                          key={message.id}
                          className={`flex ${message.sent ? 'justify-end' : 'justify-start'}`}
                        >
                          <div
                            className={`max-w-xs px-3 py-2 rounded-2xl ${
                              message.sent
                                ? 'bg-blue-500 text-white'
                                : 'bg-gray-200 text-gray-900'
                            }`}
                          >
                            <p className="text-sm">{message.text}</p>
                            <p className="text-xs mt-1 opacity-75">
                              {formatDate(message.timestamp)}
                            </p>
                          </div>
                        </div>
                      ))
                    )}
                  </div>
                </div>

                {/* SMS Message Input */}
                <div className="border-t border-gray-200 p-8">
                  <div className="flex space-x-2">
                    <Input
                      value={newMessage}
                      onChange={(e) => setNewMessage(e.target.value)}
                      placeholder="Type SMS message..."
                      className="flex-1 h-20  border-gray-300 focus:border-blue-300 focus:ring-blue-300"
                      disabled={sendingSMS || !lead?.phone}
                      onKeyDown={(e) => {
                        if (e.key === 'Enter' && !e.shiftKey) {
                          e.preventDefault();
                          handleSendMessage();
                        }
                      }}
                    />
                    <Button
                      onClick={handleSendMessage}
                      disabled={!newMessage.trim() || sendingSMS || !lead?.phone}
                      className="bg-blue-600 h-20 hover:bg-blue-700"
                    >
                      {sendingSMS ? (
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                      ) : (
                        <PaperAirplaneIcon className="h-4 w-4" />
                      )}
                    </Button>
                  </div>
                  {!lead?.phone && (
                    <p className="text-xs text-red-500 mt-2">This lead does not have a phone number</p>
                  )}
                </div>
              </>
            ) : activeTab === 'email' ? (
              <>
                {/* Email Interface */}
                <div className="flex-1 p-6">
                  {/* From/To Info */}
                  <div className="bg-gray-50 rounded-lg p-4 mb-4">
                    <div className="space-y-2">
                      <div className="flex items-center space-x-2">
                        <span className="text-sm font-medium text-gray-500 w-12">From:</span>
                        {gmailLoading ? (
                          <div className="flex items-center space-x-2">
                            <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600"></div>
                            <span className="text-sm text-gray-500">Loading...</span>
                          </div>
                        ) : connectedGmail ? (
                          <span className="text-sm text-gray-900">{connectedGmail}</span>
                        ) : (
                          <span className="text-sm text-red-500">
                            Gmail not connected. <a href="/integrations" className="text-blue-600 hover:underline">Connect in Integrations</a>
                          </span>
                        )}
                      </div>
                      <div className="flex items-center space-x-2">
                        <span className="text-sm font-medium text-gray-500 w-12">To:</span>
                        <span className="text-sm text-gray-900">{lead?.email || 'No email address'}</span>
                      </div>
                    </div>
                  </div>

                  {/* Email Composer */}
                  <div className="space-y-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Subject</label>
                      <Input
                        placeholder="Email subject..."
                        className="w-full"
                        value={emailSubject}
                        onChange={(e) => setEmailSubject(e.target.value)}
                        disabled={sendingEmail || !connectedGmail}
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Message</label>
                      <textarea
                        rows={8}
                        placeholder="Type your email message..."
                        className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none"
                        value={emailBody}
                        onChange={(e) => setEmailBody(e.target.value)}
                        disabled={sendingEmail || !connectedGmail}
                      />
                    </div>
                  </div>

                  {/* Sent Emails */}
                  {messages.filter(m => m.type === 'email').length > 0 && (
                    <div className="mt-6">
                      <h4 className="text-sm font-medium text-gray-900 mb-3">Sent Emails</h4>
                      <div className="space-y-3">
                        {messages.filter(m => m.type === 'email').map((message) => (
                          <div key={message.id} className="bg-blue-50 rounded-lg p-3 border border-blue-100">
                            <p className="text-sm text-gray-900 whitespace-pre-wrap">{message.text}</p>
                            <p className="text-xs text-gray-500 mt-2">{formatDate(message.timestamp)}</p>
                          </div>
                        ))}
                      </div>
                    </div>
                  )}
                </div>

                {/* Email Send Button */}
                <div className="border-t border-gray-200 p-4">
                  <div className="flex justify-end space-x-2">
                    <Button
                      onClick={handleSendEmail}
                      disabled={!emailSubject.trim() || !emailBody.trim() || sendingEmail || !connectedGmail || !lead?.email}
                      className="bg-blue-600 hover:bg-blue-700 flex items-center space-x-2"
                    >
                      {sendingEmail ? (
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                      ) : (
                        <PaperAirplaneIcon className="h-4 w-4" />
                      )}
                      <span>{sendingEmail ? 'Sending...' : 'Send Email'}</span>
                    </Button>
                  </div>
                  {!connectedGmail && !gmailLoading && (
                    <p className="text-xs text-red-500 mt-2 text-right">
                      Please connect Gmail in Integrations to send emails
                    </p>
                  )}
                  {!lead?.email && (
                    <p className="text-xs text-red-500 mt-2 text-right">
                      This lead does not have an email address
                    </p>
                  )}
                </div>
              </>
            ) : (
              /* Activity Tab */
              <div className="flex-1 p-2">
                <div className="space-y-4">
                  <div className="flex items-start space-x-3">
                    <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center">
                      <UserIcon className="h-4 w-4 text-green-600" />
                    </div>
                    <div className="flex-1">
                      <p className="text-sm text-gray-900">Lead created</p>
                      <p className="text-xs text-gray-500">{formatDate(lead.created_at)}</p>
                    </div>
                  </div>

                  {lead.updated_at && lead.updated_at !== lead.created_at && (
                    <div className="flex items-start space-x-3">
                      <div className="w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
                        <PencilIcon className="h-4 w-4 text-blue-600" />
                      </div>
                      <div className="flex-1">
                        <p className="text-sm text-gray-900">Lead updated</p>
                        <p className="text-xs text-gray-500">{formatDate(lead.updated_at)}</p>
                      </div>
                    </div>
                  )}

                  {/* Status Updates */}
                  <div className="mt-6">
                    <h4 className="text-sm font-medium text-gray-900 mb-3">Update Status</h4>
                    <div className="grid grid-cols-2 gap-2">
                      {statusOptions.map((status) => (
                        <Button
                          key={status.value}
                          variant="outline"
                          size="sm"
                          onClick={() => updateStatus(status.value)}
                          className={`text-xs ${
                            lead.status === status.value ? status.color : ''
                          }`}
                        >
                          {status.label}
                        </Button>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default LeadDetail;
