// src/components/ProtectedRoutes.tsx

import React, { useState, useEffect } from "react";
import { Navigate, Outlet } from "react-router-dom";
import { useAuth } from "@/context/AuthContext";
import { usePermissions } from "@/context/PermissionContext";
import Loading from "@/Loading"; // Ensure the path is correct
import FastLoading from "./FastLoading";

interface ProtectedRoutesProps {
  children?: React.ReactNode;
}

const ProtectedRoutes: React.FC<ProtectedRoutesProps> = ({ children }) => {
  const { isLoggedIn, loading: authLoading, token, user } = useAuth();
  const { loading: permissionLoading } = usePermissions();
  const [initialCheckComplete, setInitialCheckComplete] = useState(false);

  // Add a small delay to ensure authentication check is complete
  useEffect(() => {
    const timer = setTimeout(() => {
      setInitialCheckComplete(true);
    }, 100); // Small delay to ensure auth context has initialized

    return () => clearTimeout(timer);
  }, []);

  console.log(" ProtectedRoutes: Auth state check", {
    isLoggedIn,
    authLoading,
    permissionLoading,
    hasToken: !!token,
    hasUser: !!user,
    userEmail: user?.email,
    initialCheckComplete
  });

  // Show loading if auth is still loading, permissions are loading, or initial check not complete
  if (authLoading || permissionLoading || !initialCheckComplete) {
    console.log(" ProtectedRoutes: Still loading, showing FastLoading...");
    // Use FastLoading to avoid showing loading on browser refresh
    return <FastLoading delay={200} />;
  }

  if (!isLoggedIn) {
    console.log(" ProtectedRoutes: User not logged in, redirecting to login");
    // Redirect to /login if not authenticated
    return <Navigate to="/login" replace />;
  }

  console.log(" ProtectedRoutes: User authenticated, rendering protected content");
  // Render child components or nested routes if authenticated
  return children ? <>{children}</> : <Outlet />;
};

export default ProtectedRoutes;
