import React, { useState } from 'react';
import { toast } from 'sonner';
import secureLocalStorage from 'react-secure-storage';
import { useNavigate } from 'react-router-dom';
import { Button } from '../components/ui/button';
import { Input } from '../components/ui/input';
import { Upload, ArrowLeft, FileText, CheckCircle } from 'lucide-react';

const ImportLeads = () => {
  const navigate = useNavigate();
  const [importFile, setImportFile] = useState(null);
  const [csvData, setCsvData] = useState([]);
  const [csvHeaders, setCsvHeaders] = useState([]);
  const [fieldMapping, setFieldMapping] = useState({});
  const [importing, setImporting] = useState(false);

  // Handle file upload
  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (!file) return;

    if (file.type !== 'text/csv' && !file.name.endsWith('.csv')) {
      toast.error('Please select a CSV file');
      return;
    }

    setImportFile(file);
    
    const reader = new FileReader();
    reader.onload = (e) => {
      const text = e.target.result;
      const lines = text.split('\n').filter(line => line.trim());
      
      if (lines.length < 2) {
        toast.error('CSV file must contain at least a header row and one data row');
        return;
      }

      const headers = lines[0].split(',').map(header => header.replace(/"/g, '').trim());
      const data = lines.slice(1).map(line => {
        const values = line.split(',').map(value => value.replace(/"/g, '').trim());
        return headers.reduce((obj, header, index) => {
          obj[header] = values[index] || '';
          return obj;
        }, {});
      });

      setCsvHeaders(headers);
      setCsvData(data);
      
      // Auto-map common fields
      const autoMapping = {};
      headers.forEach(header => {
        const lowerHeader = header.toLowerCase();
        if (lowerHeader.includes('business') || lowerHeader.includes('company') || lowerHeader.includes('name')) {
          autoMapping[header] = 'business_name';
        } else if (lowerHeader.includes('phone') || lowerHeader.includes('mobile')) {
          autoMapping[header] = 'phone';
        } else if (lowerHeader.includes('email')) {
          autoMapping[header] = 'email';
        } else if (lowerHeader.includes('website') || lowerHeader.includes('url')) {
          autoMapping[header] = 'website';
        } else if (lowerHeader.includes('address') || lowerHeader.includes('location')) {
          autoMapping[header] = 'address';
        } else if (lowerHeader.includes('type') || lowerHeader.includes('category')) {
          autoMapping[header] = 'business_type';
        } else if (lowerHeader.includes('status')) {
          autoMapping[header] = 'status';
        }
      });
      setFieldMapping(autoMapping);
    };
    
    reader.readAsText(file);
  };

  // Handle field mapping
  const handleFieldMapping = (csvField, systemField) => {
    setFieldMapping(prev => ({
      ...prev,
      [csvField]: systemField
    }));
  };

  // Handle import execution
  const handleImportLeads = async () => {
    if (Object.keys(fieldMapping).length === 0) {
      toast.error('Please map at least one field');
      return;
    }

    setImporting(true);
    
    try {
      const token = secureLocalStorage.getItem('token');
      if (!token) {
        toast.error('Authentication required');
        return;
      }

      const mappedData = csvData.map(row => {
        const mappedRow = {};
        Object.keys(fieldMapping).forEach(csvField => {
          const systemField = fieldMapping[csvField];
          if (systemField && systemField !== 'skip') {
            mappedRow[systemField] = row[csvField] || '';
          }
        });
        
        // Add required fields for manual entries
        return {
          ...mappedRow,
          latitude: null,
          longitude: null,
          place_id: `import_${Date.now()}_${Math.random()}`,
          search_query: 'CSV Import',
          search_location: 'CSV Import',
          rating: null,
          total_ratings: null
        };
      });

      // Import leads one by one
      const importPromises = mappedData.map(leadData =>
        fetch(`${import.meta.env.VITE_APP_BASE_URL}/api/leads-contacts/save`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
          },
          body: JSON.stringify(leadData)
        })
      );

      const responses = await Promise.all(importPromises);
      const results = await Promise.all(responses.map(res => res.json()));

      const successCount = results.filter(result => result.success).length;
      const failCount = results.length - successCount;

      if (successCount > 0) {
        toast.success(`Successfully imported ${successCount} lead(s)`);
      }

      if (failCount > 0) {
        toast.error(`Failed to import ${failCount} lead(s)`);
      }

      // Navigate back to leads page
      setTimeout(() => {
        navigate('/leads-contacts');
      }, 2000);

    } catch (error) {
      console.error('Import leads error:', error);
      toast.error('Failed to import leads');
    } finally {
      setImporting(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center gap-4">
              <Button
                variant="ghost"
                size="sm"
                onClick={() => navigate('/leads-contacts')}
                className="flex items-center gap-2"
              >
                <ArrowLeft className="h-4 w-4" />
                Back to Leads
              </Button>
              <div className="h-6 w-px bg-gray-300" />
              <div className="flex items-center gap-3">
                <div className="w-8 h-8 bg-blue-100 rounded-lg flex items-center justify-center">
                  <Upload className="h-4 w-4 text-blue-600" />
                </div>
                <div>
                  <h1 className="text-xl font-semibold text-gray-900">Import Leads</h1>
                  <p className="text-sm text-gray-500">Upload and map your CSV data</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="space-y-8">
          {/* Upload Section */}
          <div className="bg-white rounded-lg shadow-sm border p-6">
            <div className="flex items-center gap-3 mb-6">
              <div className="w-8 h-8 bg-blue-100 rounded-lg flex items-center justify-center">
                <FileText className="h-4 w-4 text-blue-600" />
              </div>
              <div>
                <h2 className="text-lg font-semibold text-gray-900">Upload CSV File</h2>
                <p className="text-sm text-gray-500">Select a CSV file containing your leads data</p>
              </div>
            </div>

            <div className="border-2 border-dashed border-gray-300 rounded-lg p-8 text-center">
              <Upload className="h-12 w-12 text-gray-400 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">
                {importFile ? importFile.name : 'Choose CSV File'}
              </h3>
              <p className="text-gray-500 mb-4">
                {importFile ? `${csvData.length} records found` : 'Select a CSV file to get started'}
              </p>
              <input
                type="file"
                accept=".csv"
                onChange={handleFileUpload}
                className="hidden"
                id="csv-upload"
              />
              <label
                htmlFor="csv-upload"
                className="inline-flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 cursor-pointer"
              >
                {importFile ? 'Choose Different File' : 'Choose CSV File'}
              </label>
            </div>

            {importFile && (
              <div className="mt-6 bg-blue-50 rounded-lg p-4">
                <div className="flex items-center gap-2 text-blue-800">
                  <CheckCircle className="h-4 w-4" />
                  <span className="font-medium">File uploaded successfully!</span>
                </div>
                <p className="text-blue-700 text-sm mt-1">
                  Found {csvHeaders.length} columns and {csvData.length} records
                </p>
              </div>
            )}
          </div>

          {/* Field Mapping Section */}
          {csvHeaders.length > 0 && (
            <div className="bg-white rounded-lg shadow-sm border p-6">
              <div className="flex items-center justify-between mb-6">
                <div className="flex items-center gap-3">
                  <div className="w-8 h-8 bg-green-100 rounded-lg flex items-center justify-center">
                    <CheckCircle className="h-4 w-4 text-green-600" />
                  </div>
                  <div>
                    <h2 className="text-lg font-semibold text-gray-900">Map CSV Fields</h2>
                    <p className="text-sm text-gray-500">Match your CSV columns to our system fields</p>
                  </div>
                </div>
                <Button
                  onClick={handleImportLeads}
                  disabled={importing || Object.keys(fieldMapping).filter(key => fieldMapping[key] !== 'skip').length === 0}
                  className="bg-blue-600 hover:bg-blue-700 text-white"
                >
                  {importing ? 'Importing...' : `Import ${csvData.length} Lead${csvData.length > 1 ? 's' : ''}`}
                </Button>
              </div>

              {/* Header Row with Dropdowns */}
              <div className="space-y-4">
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                  {csvHeaders.map((header, index) => (
                    <div key={index} className="border rounded-lg p-4 bg-gray-50">
                      <div className="mb-3">
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          CSV Column
                        </label>
                        <div className="text-sm font-semibold text-gray-900 bg-white px-3 py-2 rounded border">
                          {header}
                        </div>
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          Map to Field
                        </label>
                        <select
                          value={fieldMapping[header] || ''}
                          onChange={(e) => handleFieldMapping(header, e.target.value)}
                          className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                        >
                          <option value="">Select field to map</option>
                          <option value="business_name">Business Name</option>
                          <option value="phone">Phone</option>
                          <option value="email">Email</option>
                          <option value="website">Website</option>
                          <option value="address">Address</option>
                          <option value="business_type">Business Type</option>
                          <option value="status">Status</option>
                          <option value="skip">Skip this field</option>
                        </select>
                      </div>

                      {/* Preview Data */}
                      {csvData.length > 0 && (
                        <div className="mt-3">
                          <label className="block text-xs font-medium text-gray-500 mb-1">
                            Sample Data
                          </label>
                          <div className="text-xs text-gray-600 bg-white px-2 py-1 rounded border truncate">
                            {csvData[0][header] || 'No data'}
                          </div>
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              </div>

              {/* Requirements */}
              <div className="mt-6 bg-gray-50 rounded-lg p-4">
                <h4 className="font-medium text-gray-900 mb-2">CSV Format Requirements:</h4>
                <ul className="text-sm text-gray-600 space-y-1">
                  <li> First row should contain column headers</li>
                  <li> Supported fields: Business Name, Phone, Email, Website, Address, Business Type, Status</li>
                  <li> Use comma-separated values (.csv format)</li>
                  <li> Maximum file size: 10MB</li>
                </ul>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default ImportLeads;
