import axiosInstance from '../../axios/axiosInstance';

export interface Place {
  place_id: string;
  name: string;
  formatted_address: string;
  rating?: number;
  user_ratings_total?: number;
  price_level?: number;
  types: string[];
  geometry: {
    location: {
      lat: number;
      lng: number;
    };
  };
  photos?: any[];
  opening_hours?: any;
  business_status?: string;
  search_query?: string;
  search_location?: string;
}

export interface ScrapedLead {
  id: number;
  user_id: number;
  business_name: string;
  address: string;
  phone?: string;
  website?: string;
  rating?: number;
  total_ratings?: number;
  business_type?: string;
  latitude?: number;
  longitude?: number;
  place_id?: string;
  google_maps_url?: string;
  opening_hours?: string;
  price_level?: number;
  photos?: string;
  reviews?: string;
  search_query?: string;
  search_location?: string;
  status: 'new' | 'contacted' | 'interested' | 'not_interested';
  notes?: string;
  created_at: string;
  updated_at: string;
}

export interface SearchPlacesRequest {
  query: string;
  location?: string;
  radius?: number;
  type?: string;
  maxResults?: number;
  openNow?: boolean;
  priceLevel?: number;
  minRating?: number;
  pageToken?: string;  // For pagination - next page token
}

export interface SearchPlacesResponse {
  success: boolean;
  data: {
    places: Place[];
    search_query: string;
    search_location: string;
    total_results: number;
    nextPageToken?: string;  // Token for fetching next page
    hasMore: boolean;  // Whether there are more results to fetch
  };
}

export interface PlaceDetailsResponse {
  success: boolean;
  data: {
    name: string;
    formatted_address: string;
    formatted_phone_number?: string;
    website?: string;
    rating?: number;
    user_ratings_total?: number;
    price_level?: number;
    opening_hours?: any;
    geometry: {
      location: {
        lat: number;
        lng: number;
      };
    };
    photos?: any[];
    reviews?: any[];
    types: string[];
    business_status?: string;
    url?: string;
  };
}

export interface LeadsResponse {
  success: boolean;
  data: {
    leads: ScrapedLead[];
    totalLeads: number;
    currentPage: number;
    totalPages: number;
  };
}

export interface LeadStatsResponse {
  success: boolean;
  data: {
    total_leads: number;
    new_leads: number;
    contacted_leads: number;
    interested_leads: number;
    not_interested_leads: number;
    avg_rating: number;
    unique_searches: number;
  };
}

export interface SaveLeadRequest {
  business_name?: string;
  name?: string;
  address?: string;
  formatted_address?: string;
  phone?: string;
  formatted_phone_number?: string;
  website?: string;
  rating?: number;
  total_ratings?: number;
  user_ratings_total?: number;
  business_type?: string;
  types?: string[];
  latitude?: number;
  longitude?: number;
  geometry?: {
    location: {
      lat: number;
      lng: number;
    };
  };
  place_id?: string;
  google_maps_url?: string;
  url?: string;
  opening_hours?: any;
  price_level?: number;
  photos?: any[];
  reviews?: any[];
  search_query?: string;
  search_location?: string;
  status?: string;
  notes?: string;
}

export interface BulkSaveLeadsRequest {
  leads: SaveLeadRequest[];
}

export interface LeadFilters {
  page?: number;
  limit?: number;
  search?: string;
  status?: string;
  business_type?: string;
  rating_min?: number;
  search_query?: string;
}

export const leadScraperService = {
  // Search for places using Google Maps API


  searchPlaces: async (searchData: SearchPlacesRequest): Promise<SearchPlacesResponse> => {
    const response = await axiosInstance.post('/api/lead-scraper/search', searchData);
    console.log(response,"============ check google response=======")
    return response.data;
  },

  // Get detailed information about a specific place
  getPlaceDetails: async (placeId: string): Promise<PlaceDetailsResponse> => {
    const response = await axiosInstance.get(`/api/lead-scraper/place/${placeId}`);
    return response.data;
  },

  // Save a lead to database
  saveLead: async (leadData: SaveLeadRequest) => {
    const response = await axiosInstance.post('/api/lead-scraper/leads', leadData);
    return response.data;
  },

  // Get all saved leads for the user
  getLeads: async (filters: LeadFilters = {}): Promise<LeadsResponse> => {
    const params = new URLSearchParams();
    Object.entries(filters).forEach(([key, value]) => {
      if (value !== undefined && value !== null && value !== '') {
        params.append(key, value.toString());
      }
    });
    
    const response = await axiosInstance.get(`/api/lead-scraper/leads?${params.toString()}`);
    return response.data;
  },

  // Get a specific lead by ID
  getLead: async (leadId: number) => {
    const response = await axiosInstance.get(`/api/lead-scraper/leads/${leadId}`);
    return response.data;
  },

  // Update a lead
  updateLead: async (leadId: number, updates: Partial<SaveLeadRequest>) => {
    const response = await axiosInstance.put(`/api/lead-scraper/leads/${leadId}`, updates);
    return response.data;
  },

  // Delete a lead
  deleteLead: async (leadId: number) => {
    const response = await axiosInstance.delete(`/api/lead-scraper/leads/${leadId}`);
    return response.data;
  },

  // Get lead statistics
  getStats: async (): Promise<LeadStatsResponse> => {
    const response = await axiosInstance.get('/api/lead-scraper/stats');
    return response.data;
  },

  // Bulk save leads
  bulkSaveLeads: async (leadsData: BulkSaveLeadsRequest) => {
    const response = await axiosInstance.post('/api/lead-scraper/leads/bulk', leadsData);
    return response.data;
  }
};
