import { QueryClient } from '@tanstack/react-query';

export class CacheManager {
  private static instance: CacheManager;
  private queryClient: QueryClient | null = null;

  private constructor() {}

  public static getInstance(): CacheManager {
    if (!CacheManager.instance) {
      CacheManager.instance = new CacheManager();
    }
    return CacheManager.instance;
  }

  public setQueryClient(client: QueryClient) {
    this.queryClient = client;
  }

  // Clear all cache
  public clearAllCache(): void {
    if (this.queryClient) {
      console.log(' Clearing all React Query cache...');
      this.queryClient.clear();
    }
    
    // Clear browser storage
    this.clearBrowserCache();
  }

  // Clear specific query cache
  public clearQueryCache(queryKey: string[]): void {
    if (this.queryClient) {
      console.log(` Clearing cache for query: ${queryKey.join(', ')}`);
      this.queryClient.removeQueries({ queryKey });
    }
  }

  // Invalidate specific queries
  public invalidateQueries(queryKey: string[]): void {
    if (this.queryClient) {
      console.log(` Invalidating queries: ${queryKey.join(', ')}`);
      this.queryClient.invalidateQueries({ queryKey });
    }
  }

  // Invalidate queries by page/route
  public invalidateQueriesByPage(pagePath: string): void {
    if (this.queryClient) {
      const cache = this.queryClient.getQueryCache();
      const queries = cache.getAll();

      // Find queries that might be related to this page
      const pageQueries = queries.filter(query => {
        const queryKey = query.queryKey;
        return queryKey.some(key =>
          typeof key === 'string' &&
          (key.includes(pagePath.replace('/', '')) ||
           pagePath.includes(key))
        );
      });

      pageQueries.forEach(query => {
        console.log(` Invalidating page-specific query: ${query.queryKey.join(', ')}`);
        this.queryClient?.invalidateQueries({ queryKey: query.queryKey });
      });
    }
  }

  // Clear browser cache (localStorage, sessionStorage)
  public clearBrowserCache(): void {
    try {
      console.log(' Clearing browser storage (preserving auth tokens)...');

      // Clear localStorage (except secure storage items and auth tokens)
      const keysToKeep = ['authToken', 'token', 'react-secure-storage'];
      const localStorageKeys = Object.keys(localStorage);
      localStorageKeys.forEach(key => {
        if (!keysToKeep.includes(key) && !key.startsWith('react-secure-storage')) {
          localStorage.removeItem(key);
        }
      });

      // Clear sessionStorage (but preserve any auth-related items)
      const sessionKeysToKeep = ['authToken', 'token'];
      const sessionStorageKeys = Object.keys(sessionStorage);
      sessionStorageKeys.forEach(key => {
        if (!sessionKeysToKeep.includes(key)) {
          sessionStorage.removeItem(key);
        }
      });

      console.log(' Browser cache cleared successfully (auth tokens preserved)');
    } catch (error) {
      console.error(' Error clearing browser cache:', error);
    }
  }

  // Auto-clear stale cache entries
  public autoCleanStaleCache(): void {
    if (this.queryClient) {
      const cache = this.queryClient.getQueryCache();
      const queries = cache.getAll();

      let clearedCount = 0;
      queries.forEach(query => {
        const state = query.state;
        const now = Date.now();
        const fiveMinutesAgo = now - (5 * 60 * 1000);

        // Remove queries that are older than 5 minutes and not currently being used
        if (query.state.dataUpdatedAt && query.state.dataUpdatedAt < fiveMinutesAgo && state.status !== 'pending') {
          cache.remove(query);
          clearedCount++;
        }
      });

      if (clearedCount > 0) {
        console.log(` Auto-cleaned ${clearedCount} stale cache entries`);
      }
    }
  }

  // Clear stale cache (alias for autoCleanStaleCache for browser refresh)
  public clearStaleCache(): void {
    console.log(' Clearing stale cache entries...');
    this.autoCleanStaleCache();
  }

  // Force refresh specific data
  public forceRefresh(queryKey: string[]): void {
    if (this.queryClient) {
      console.log(` Force refreshing: ${queryKey.join(', ')}`);
      this.queryClient.refetchQueries({ queryKey });
    }
  }

  // Get cache statistics
  public getCacheStats(): { totalQueries: number; staleQueries: number; activeQueries: number } {
    if (!this.queryClient) {
      return { totalQueries: 0, staleQueries: 0, activeQueries: 0 };
    }

    const cache = this.queryClient.getQueryCache();
    const queries = cache.getAll();
    const now = Date.now();
    const fiveMinutesAgo = now - (5 * 60 * 1000);

    const stats = {
      totalQueries: queries.length,
      staleQueries: queries.filter(q => q.state.dataUpdatedAt && q.state.dataUpdatedAt < fiveMinutesAgo).length,
      activeQueries: queries.filter(q => q.state.status === 'pending').length
    };

    return stats;
  }

  // Clear cache for page navigation
  public clearCacheForNavigation(): void {
    if (this.queryClient) {
      console.log(' Clearing cache for page navigation...');

      // Clear all queries except authentication-related ones
      const cache = this.queryClient.getQueryCache();
      const queries = cache.getAll();

      queries.forEach(query => {
        const queryKey = query.queryKey;
        const isAuthQuery = queryKey.some(key =>
          typeof key === 'string' &&
          (key.includes('auth') || key.includes('user') || key.includes('permission'))
        );

        if (!isAuthQuery) {
          cache.remove(query);
        }
      });

      console.log(' Navigation cache clear completed');
    }
  }

  // Setup automatic cache management
  public setupAutoCacheManagement(): void {
    // Auto-clean stale cache every 2 minutes
    setInterval(() => {
      this.autoCleanStaleCache();
    }, 2 * 60 * 1000);

    // Disabled full cache clear to preserve authentication state
    // Full cache clear every 30 minutes (less frequent to preserve auth)
    setInterval(() => {
      console.log(' Performing scheduled stale cache cleanup (preserving auth)...');
      this.autoCleanStaleCache(); // Use stale cache cleanup instead of full clear
    }, 30 * 60 * 1000);

    // Log cache stats every 5 minutes in development
    if (process.env.NODE_ENV === 'development') {
      setInterval(() => {
        const stats = this.getCacheStats();
        console.log(' Cache Stats:', stats);
      }, 5 * 60 * 1000);
    }
  }
}

// Export singleton instance
export const cacheManager = CacheManager.getInstance();
