import React, { useState } from "react";
import axiosInstance from "@/api/axios/axiosInstance";

const TOTAL_STEPS = 6;

const StudyAnalysisStepper = ({ study }) => {
  const [step, setStep] = useState(0);
  const [analysis, setAnalysis] = useState({});
  const [loading, setLoading] = useState(false);

  if (!study) return null;

  const delay = (ms) => new Promise(res => setTimeout(res, ms));

  const buildFinalPayload = (protocol, difficulty, cost, confidence) => ({
    nctId: study.protocolSection.identificationModule.nctId,

    phase: protocol.phase,
    studyType: protocol.studyType,
    conditionCount: protocol.conditionCount,
    eligibilityLength: protocol.eligibilityLength,
    durationMonths: protocol.durationMonths,

    complexityLabel: protocol.complexityLabel,
    difficultyLabel: difficulty.label,

    enrollment: protocol.enrollment,
    cpe: cost.cpe,
    estimatedTotalCost: cost.estimatedTotalCost,

    confidence: confidence.confidence,

    geo: "US",
    siteCount: 10
  });

  // const startCalculation = async () => {
  //   try {
  //     setLoading(true);
  //     setAnalysis({});
  //     setStep(1);

  //     const protocol = await axiosInstance.post(
  //       "/api/studies/analyze-protocol",
  //       { study }
  //     );
  //     setAnalysis(protocol.data);
  //     await delay(600);

  //     setStep(2);
  //     const difficulty = await axiosInstance.post(
  //       "/api/studies/difficulty",
  //       protocol.data
  //     );
  //     setAnalysis(p => ({ ...p, ...difficulty.data }));
  //     await delay(600);

  //     setStep(3);
  //     const cost = await axiosInstance.post("/api/studies/cost", {
  //       difficultyLabel: difficulty.data.label,
  //       enrollment: protocol.data.enrollment
  //     });
  //     setAnalysis(p => ({ ...p, ...cost.data }));
  //     await delay(600);

  //     setStep(4);
  //     const confidence = await axiosInstance.post(
  //       "/api/studies/confidence",
  //       protocol.data
  //     );
  //     setAnalysis(p => ({ ...p, ...confidence.data }));
  //     await delay(600);

  //     setStep(5);

  //     setStep(6);
  //     const finalPayload = buildFinalPayload(
  //       protocol.data,
  //       difficulty.data,
  //       cost.data,
  //       confidence.data
  //     );

    

  //   } catch (e) {
  //     console.error(e);
  //     setStep(0);
  //   } finally {
  //     setLoading(false);
  //   }
  // };



  const startCalculation = async () => {
    try {
      setLoading(true);
      setAnalysis({});
      setStep(1);
  
      const protocol = await axiosInstance.post(
        "/api/studies/analyze-protocol",
        { study }
      );
      setAnalysis(protocol.data);
      await delay(500);
  
      setStep(2);
      const difficulty = await axiosInstance.post(
        "/api/studies/difficulty",
        protocol.data
      );
      setAnalysis(p => ({ ...p, ...difficulty.data }));
      await delay(500);
  
      setStep(3);
      const cost = await axiosInstance.post("/api/studies/cost", {
        difficultyLabel: difficulty.data.label,
        enrollment: protocol.data.enrollment
      });
      setAnalysis(p => ({ ...p, ...cost.data }));
      await delay(500);
  
      setStep(4);
      const confidence = await axiosInstance.post(
        "/api/studies/confidence",
        protocol.data
      );
      setAnalysis(p => ({ ...p, ...confidence.data }));
      await delay(500);
  
      const finalPayload = buildFinalPayload(
        protocol.data,
        difficulty.data,
        cost.data,
        confidence.data
      );
  
      setAnalysis(p => ({ ...p, finalPayload }));
      setStep(6);
    } catch (e) {
      console.error(e);
      setStep(0);
    } finally {
      setLoading(false);
    }
  };
  
  const downloadProposal = async () => {
    const res = await axiosInstance.post(
      "/api/studies/proposal/pdf",
      analysis.finalPayload,
      { responseType: "blob" }
    );

    const blob = new Blob([res.data], { type: "application/pdf" });
    const url = window.URL.createObjectURL(blob);

    const a = document.createElement("a");
    a.href = url;
    a.download = `proposal-${analysis.finalPayload.nctId}.pdf`;
    document.body.appendChild(a);
    a.click();
    a.remove();
    window.URL.revokeObjectURL(url);
  };

  return (
    <div className="bg-white p-6 rounded-lg shadow space-y-6">

      <div className="flex justify-between">
        <h3 className="text-lg font-semibold">
          Predictive Enrollment Engineering
        </h3>

        <button
          onClick={startCalculation}
          disabled={loading}
          className="px-4 py-2 bg-blue-600 text-white rounded"
        >
          {loading ? "Processing" : "Calculate"}
        </button>
      </div>

      <div className="h-2 bg-gray-200 rounded">
        <div
          className="h-full bg-blue-600"
          style={{ width: `${(step / TOTAL_STEPS) * 100}%` }}
        />
      </div>

      {step >= 5 && (
        <div className="bg-blue-600 text-white p-5 rounded">
          <p><b>Confidence:</b> {analysis.confidence}%</p>
          <p><b>Difficulty:</b> {analysis.label}</p>
          <p><b>CPE:</b> ${analysis.cpe}</p>
          <p><b>Total Cost:</b> ${analysis.estimatedTotalCost?.toLocaleString()}</p>
        </div>
      )}

      {step === 6 && (
        <button
          onClick={downloadProposal}
          className="px-4 py-2 bg-green-600 text-white rounded"
        >
          Download Proposal PDF
        </button>
      )}
    </div>
  );
};

export default StudyAnalysisStepper;
