
import axios from 'axios';
import secureLocalStorage from 'react-secure-storage';

console.log(" Environment VITE_APP_BASE_URL:", import.meta.env.VITE_APP_BASE_URL);

// Set baseURL for API calls
// Use '/api' for development (Vite proxy case)
// Use VITE_APP_BASE_URL if set (production case)
const baseURL = import.meta.env.VITE_APP_BASE_URL || '/api';

console.log(" Using baseURL:", baseURL);

const axiosInstance = axios.create({
    baseURL: baseURL,
});

console.log(" Axios instance created with baseURL:", axiosInstance.defaults.baseURL);

axiosInstance.interceptors.request.use(
    (config) => {
        try {
            // Try both token keys for compatibility
            const token = secureLocalStorage.getItem('token') || secureLocalStorage.getItem('authToken') || localStorage.getItem('token');
            const fullUrl = `${config.baseURL}${config.url}`;
            console.log(" Axios interceptor - Full URL:", fullUrl);
            console.log(" Axios interceptor - Base URL:", config.baseURL);
            console.log(" Axios interceptor - Request URL:", config.url);
            console.log(" Axios interceptor - Token found:", !!token);
            console.log(" Axios interceptor - Token preview:", token ? String(token).substring(0, 20) + '...' : 'null');

            if (token && typeof token === 'string') {
                config.headers['Authorization'] = `Bearer ${token}`;
                console.log(" Axios interceptor - Authorization header set");
            } else {
                console.log(" Axios interceptor - No valid token found in storage");
                console.log(" Available localStorage keys:", Object.keys(localStorage));
            }
        } catch (error) {
            console.error(" Axios interceptor - Error accessing storage:", error);
        }
        return config;
    },
    (error) => {
        console.error(" Axios interceptor error:", error);
        return Promise.reject(error);
    }
);

// Add response interceptor for debugging
axiosInstance.interceptors.response.use(
    (response) => {
        if (response.config.url?.includes('/api/credits/balance')) {
            console.log(" Axios response - Credit balance API response:", response.data);
        }
        return response;
    },
    (error) => {
        if (error.config?.url?.includes('/api/credits/balance')) {
            console.error(" Axios response - Credit balance API error:", error.response?.data);
        }
        return Promise.reject(error);
    }
);

export default axiosInstance;