import { useEffect, useState } from 'react';

/**
 * Hook to detect browser refresh and handle cache accordingly
 */
export function useBrowserRefresh() {
  const [isRefresh, setIsRefresh] = useState(false);
  const [isInitialLoad, setIsInitialLoad] = useState(true);

  useEffect(() => {
    // Check if this is a browser refresh
    const navigationEntries = performance.getEntriesByType('navigation') as PerformanceNavigationTiming[];
    
    if (navigationEntries.length > 0) {
      const navEntry = navigationEntries[0];
      
      // Check if it's a reload (browser refresh)
      if (navEntry.type === 'reload') {
        setIsRefresh(true);
        console.log(' Browser refresh detected - preserving authentication');

        // Clear cache on browser refresh for fresh data, but preserve auth tokens
        import('../utils/cacheManager').then(({ cacheManager }) => {
          // Don't clear all cache on refresh to preserve authentication state
          cacheManager.clearStaleCache();
          console.log(' Stale cache cleared due to browser refresh (auth preserved)');
        }).catch((error) => {
          console.warn('Could not clear stale cache on refresh:', error);
        });
      } else {
        setIsRefresh(false);
      }
    }

    // Mark initial load as complete
    const timer = setTimeout(() => {
      setIsInitialLoad(false);
    }, 500);

    return () => clearTimeout(timer);
  }, []);

  // Handle page visibility change (when user switches tabs)
  useEffect(() => {
    const handleVisibilityChange = () => {
      if (!document.hidden && !isInitialLoad) {
        // Page became visible again, refresh stale data
        console.log(' Page became visible, refreshing stale data...');
        
        import('../utils/cacheManager').then(({ cacheManager }) => {
          cacheManager.autoCleanStaleCache();
        }).catch((error) => {
          console.warn('Could not clean stale cache on visibility change:', error);
        });
      }
    };

    document.addEventListener('visibilitychange', handleVisibilityChange);
    
    return () => {
      document.removeEventListener('visibilitychange', handleVisibilityChange);
    };
  }, [isInitialLoad]);

  // Handle online/offline events
  useEffect(() => {
    const handleOnline = () => {
      console.log(' Connection restored, refreshing data...');
      
      import('../utils/cacheManager').then(({ cacheManager }) => {
        cacheManager.forceRefresh(['dashboard', 'agents', 'calls']);
      }).catch((error) => {
        console.warn('Could not refresh data on reconnection:', error);
      });
    };

    const handleOffline = () => {
      console.log(' Connection lost');
    };

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);
    
    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  return {
    isRefresh,
    isInitialLoad,
    isOnline: navigator.onLine,
  };
}
