import { memo, useEffect, useState } from "react";

interface FastLoadingProps {
  delay?: number; // Delay before showing loading (in ms)
  minDuration?: number; // Minimum duration to show loading (in ms)
}

const FastLoading = memo(({ delay = 200, minDuration = 200 }: FastLoadingProps) => {
  const [showLoading, setShowLoading] = useState(false);
  const [startTime, setStartTime] = useState<number | null>(null);

  useEffect(() => {
    // Only show loading after a delay to avoid flash for fast loads
    const delayTimer = setTimeout(() => {
      setShowLoading(true);
      setStartTime(Date.now());
    }, delay);

    return () => {
      clearTimeout(delayTimer);
    };
  }, [delay]);

  useEffect(() => {
    // Ensure minimum duration if loading is shown
    if (showLoading && startTime) {
      const elapsed = Date.now() - startTime;
      if (elapsed < minDuration) {
        const remainingTime = minDuration - elapsed;
        const timer = setTimeout(() => {
          // This will be handled by the parent component unmounting
        }, remainingTime);
        
        return () => clearTimeout(timer);
      }
    }
  }, [showLoading, startTime, minDuration]);

  // Don't show anything if we're still in the delay period
  if (!showLoading) {
    return null;
  }

  return (
    <div className="fixed inset-0 flex items-center justify-center bg-white">
    <div className="flex flex-col items-center gap-8">
      {/* Advanced Loader Animation */}
      <div className="relative">
        {/* Outer rotating ring */}
        <div className="w-20 h-20 border-4 border-gray-200 rounded-full animate-spin" />
        
        {/* Middle pulsing ring */}
        <div className="absolute inset-2 w-16 h-16 border-4 border-t-purple-500 border-r-blue-500 border-b-cyan-500 border-l-purple-500 rounded-full animate-spin" 
             style={{ animationDirection: 'reverse', animationDuration: '1.5s' }} />
        
        {/* Inner glowing core */}
        <div className="absolute inset-6 w-8 h-8 bg-gradient-to-r from-purple-500 to-cyan-500 rounded-full animate-pulse shadow-lg shadow-purple-500/50" />
        
        {/* Orbiting dots */}
        <div className="absolute inset-0 w-20 h-20 animate-spin" style={{ animationDuration: '3s' }}>
          <div className="absolute top-0 left-1/2 w-2 h-2 bg-purple-500 rounded-full transform -translate-x-1/2 animate-pulse" />
          <div className="absolute bottom-0 left-1/2 w-2 h-2 bg-cyan-500 rounded-full transform -translate-x-1/2 animate-pulse" />
        </div>
        
        {/* Outer glow effect */}
        <div className="absolute inset-0 w-20 h-20 rounded-full bg-gradient-to-r from-purple-500/20 to-cyan-500/20 blur-sm animate-pulse" />
      </div>

      {/* Animated Text */}
      <div className="text-center">
        <h2 className="text-4xl font-bold bg-gradient-to-r from-purple-600 via-blue-600 to-cyan-600 bg-clip-text text-transparent animate-pulse mb-2">
          AI CRUITMENT
        </h2>
        
        {/* Loading dots animation */}
        <div className="flex items-center justify-center gap-1 mt-4">
          <span className="text-gray-600 text-lg">Loading</span>
          <div className="flex gap-1 ml-1">
            <div className="w-1 h-1 bg-purple-500 rounded-full animate-bounce" style={{ animationDelay: '0ms' }} />
            <div className="w-1 h-1 bg-blue-500 rounded-full animate-bounce" style={{ animationDelay: '150ms' }} />
            <div className="w-1 h-1 bg-cyan-500 rounded-full animate-bounce" style={{ animationDelay: '300ms' }} />
          </div>
        </div>
      </div>

      {/* Progress bar */}
      <div className="w-64 h-1 bg-gray-200 rounded-full overflow-hidden">
        <div
          className="h-full bg-gradient-to-r from-purple-500 to-cyan-500 rounded-full animate-pulse"
          style={{
            width: '60%',
            animation: 'progressBar 2s ease-in-out infinite'
          }}
        />
      </div>
    </div>

    {/* Background particles */}
    <div className="absolute inset-0 overflow-hidden pointer-events-none">
      <div className="absolute top-1/4 left-1/4 w-2 h-2 bg-purple-500/20 rounded-full animate-ping"
           style={{ animationDelay: '0s', animationDuration: '3s' }} />
      <div className="absolute top-1/3 right-1/4 w-1 h-1 bg-cyan-500/30 rounded-full animate-ping"
           style={{ animationDelay: '1s', animationDuration: '4s' }} />
      <div className="absolute bottom-1/3 left-1/3 w-1.5 h-1.5 bg-blue-500/20 rounded-full animate-ping"
           style={{ animationDelay: '2s', animationDuration: '5s' }} />
      <div className="absolute bottom-1/4 right-1/3 w-1 h-1 bg-purple-500/15 rounded-full animate-ping"
           style={{ animationDelay: '0.5s', animationDuration: '3.5s' }} />
    </div>

    {/* Custom CSS for progress animation */}
    <style dangerouslySetInnerHTML={{
      __html: `
        @keyframes progressBar {
          0%, 100% { transform: translateX(-100%); }
          50% { transform: translateX(300%); }
        }
      `
    }} />
  </div>
  );
});

FastLoading.displayName = "FastLoading";

export default FastLoading;
