// src/context/AuthContext.tsx

import React, { createContext, useContext, useState, useEffect, useRef } from "react";
import secureLocalStorage from "react-secure-storage";
import { jwtDecode } from "jwt-decode";
import { useNavigate } from "react-router-dom";
import { useQueryClient } from "@tanstack/react-query";
import axiosInstance from "@/api/axios/axiosInstance";

interface User {
  id: number;
  email: string;
  name?: string;
  first_name?: string;
  last_name?: string;
  username?: string;
  role?: string;
  role_id?: number;
  org_id?: string;
  phone_number?: string;
  created_at?: string;
  last_login?: string;
  isSuperAdmin?: boolean;
}

interface AuthContextType {
  isLoggedIn: boolean;
  token: string | null;
  user: User | null;
  login: (token: string) => void;
  logout: () => void;
  loading: boolean;
}

// Define the structure of your JWT payload
interface JwtPayload {
  exp: number; // Expiration time (in seconds since epoch)
  // User data can be nested in different ways depending on login type
  id?: number;
  email?: string;
  name?: string;
  first_name?: string;
  last_name?: string;
  username?: string;
  role?: string;
  org_id?: string;
  phone_number?: string;
  created_at?: string;
  last_login?: string;
  user?: {
    id: number;
    email: string;
    first_name?: string;
    last_name?: string;
    username?: string;
    role_id?: number;
  };
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [token, setToken] = useState<string | null>(null);
  const [user, setUser] = useState<User | null>(null);
  const [isLoggedIn, setIsLoggedIn] = useState<boolean>(false);
  const [isLoggingOut, setIsLoggingOut] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(true); // Initialize loading as true
  const navigate = useNavigate();
  const queryClient = useQueryClient();

  // Extract user data from JWT token
  const extractUserFromToken = (token: string): User | null => {
    try {
      const decoded: JwtPayload = jwtDecode(token);

      // Handle different token structures
      let userData: User;

      if (decoded.user) {
        // Regular login structure
        userData = {
          id: decoded.user.id,
          email: decoded.user.email,
          first_name: decoded.user.first_name,
          last_name: decoded.user.last_name,
          username: decoded.user.username,
          role: decoded.user.role_id?.toString(),
          role_id: decoded.user.role_id,
          isSuperAdmin: (decoded.user as any).isSuperAdmin,
        };
      } else {
        // Demo login or direct structure
        userData = {
          id: decoded.id || 0,
          email: decoded.email || '',
          name: decoded.name,
          first_name: decoded.first_name,
          last_name: decoded.last_name,
          username: decoded.username,
          role: decoded.role,
          org_id: decoded.org_id,
          phone_number: decoded.phone_number,
          created_at: decoded.created_at,
          last_login: decoded.last_login,
          isSuperAdmin: (decoded as any).isSuperAdmin,
        };
      }

      return userData;
    } catch (error) {
      console.error("Error extracting user from token:", error);
      return null;
    }
  };

  // Prevent duplicate audit logs with debounce
  const auditLogCache = useRef<Set<string>>(new Set());

  // Helper function to create audit log entry (with duplicate prevention)
  const createAuditLogEntry = async (operation: 'LOGIN' | 'LOGOUT', userData?: User | null): Promise<void> => {
    return new Promise(async (resolve, reject) => {
      try {
        // Ensure we have user data
        if (!userData) {
          console.warn('No user data provided for audit log');
          resolve();
          return;
        }

        // Create a unique key to prevent duplicates
        const cacheKey = `${operation}_${userData.id}_${userData.email}_${Date.now().toString().slice(0, -3)}`; // Remove last 3 digits for 1-second window

        if (auditLogCache.current.has(cacheKey)) {
          console.log(' Duplicate audit log prevented for:', operation, userData.email);
          resolve();
          return;
        }

        // Add to cache to prevent duplicates
        auditLogCache.current.add(cacheKey);

        // Clean up old cache entries (keep only last 10)
        if (auditLogCache.current.size > 10) {
          const entries = Array.from(auditLogCache.current);
          auditLogCache.current.clear();
          entries.slice(-5).forEach(entry => auditLogCache.current.add(entry));
        }

        // Extract browser information
        const getBrowserInfo = () => {
          const userAgent = navigator.userAgent;
          if (userAgent.includes('Chrome')) return 'Chrome';
          if (userAgent.includes('Firefox')) return 'Firefox';
          if (userAgent.includes('Safari') && !userAgent.includes('Chrome')) return 'Safari';
          if (userAgent.includes('Edge')) return 'Edge';
          if (userAgent.includes('Opera')) return 'Opera';
          return 'Other';
        };

        const auditData = {
          operation_type: 'CREATE' as const,
          table_name: 'auth_sessions',
          record_id: userData.id?.toString() || 'unknown',
          // Explicitly set user information in the audit data
          user_email: userData.email,
          user_name: `${userData.first_name || ''} ${userData.last_name || ''}`.trim() || userData.email,
          old_values: operation === 'LOGOUT' ? {
            session_active: true,
            user_authenticated: true,
            user_id: userData.id,
            user_email: userData.email,
            user_name: `${userData.first_name || ''} ${userData.last_name || ''}`.trim()
          } : null,
          new_values: {
            action: operation,
            user_id: userData.id,
            user_email: userData.email,
            user_name: `${userData.first_name || ''} ${userData.last_name || ''}`.trim() || userData.email,
            timestamp: new Date().toISOString(),
            session_active: operation === 'LOGIN',
            user_authenticated: operation === 'LOGIN',
            browser: getBrowserInfo(),
            platform: navigator.platform,
            user_agent: navigator.userAgent
          },
          metadata: {
            authentication_event: true,
            action: operation,
            session_type: 'web',
            browser: getBrowserInfo(),
            platform: navigator.platform,
            screen_resolution: `${screen.width}x${screen.height}`,
            timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
            language: navigator.language,
            // Include user info in metadata as backup
            user_info: {
              id: userData.id,
              email: userData.email,
              name: `${userData.first_name || ''} ${userData.last_name || ''}`.trim()
            },
            // Add cache key for debugging
            cache_key: cacheKey
          }
        };

        console.log(' Creating audit log for:', operation, 'User:', userData.email, 'Key:', cacheKey);

        // Await the audit log creation
        const response = await axiosInstance.post('/api/audit-logs', auditData);
        console.log(' Audit log created successfully:', response.data);
        resolve();
      } catch (error) {
        console.error(' Error creating audit log entry:', error);
        reject(error);
      }
    });
  };

  const logout = async () => {
    // Prevent multiple simultaneous logout calls
    if (isLoggingOut) {
      console.log(' Logout already in progress, skipping...');
      return;
    }

    setIsLoggingOut(true);

    try {
      // Create audit log entry BEFORE clearing user data
      if (user) {
        try {
          // Wait for the audit log to be created before proceeding
          await createAuditLogEntry('LOGOUT', user);
          console.log(' Logout audit log created for user:', user.email);
        } catch (error) {
          console.warn(' Failed to create logout audit log:', error);
        }
      }

      // Clear all storage
      secureLocalStorage.removeItem("token");
      secureLocalStorage.removeItem("authToken");

      // Clear React Query cache to prevent cross-user data contamination
      queryClient.clear();

      // Also clear browser cache
      import('../utils/cacheManager').then(({ cacheManager }) => {
        cacheManager.clearBrowserCache();
      }).catch((error) => {
        console.warn('Could not clear browser cache:', error);
      });

      // Reset auth state
      setToken(null);
      setUser(null);
      setIsLoggedIn(false);

      // Redirect to login
      navigate("/login");
    } finally {
      // Reset logout state after a delay to prevent rapid re-calls
      setTimeout(() => {
        setIsLoggingOut(false);
      }, 1000);
    }
  };

  const checkTokenValidity = (storedToken: string) => {
    console.log(" AuthContext: Checking token validity...");
    try {
      const decoded: JwtPayload = jwtDecode(storedToken);
      const currentTime = Date.now() / 1000; // Current time in seconds
      const timeUntilExpiry = decoded.exp - currentTime;

      console.log(" AuthContext: Token expires in", Math.floor(timeUntilExpiry / 60), "minutes");

      if (decoded.exp < currentTime) {
        console.log(" AuthContext: Token has expired.");
        // Clear expired token
        secureLocalStorage.removeItem("token");
        secureLocalStorage.removeItem("authToken");
        setToken(null);
        setUser(null);
        setIsLoggedIn(false);
      } else {
        console.log(" AuthContext: Token is valid, extracting user data...");
        const userData = extractUserFromToken(storedToken);
        if (userData) {
          setToken(storedToken);
          setUser(userData);
          setIsLoggedIn(true);
          console.log(" AuthContext: User authenticated successfully:", userData.email);
        } else {
          console.log(" AuthContext: Failed to extract user data from token");
          setIsLoggedIn(false);
        }
      }
    } catch (error) {
      console.error(" AuthContext: Error decoding token:", error);
      // Clear invalid token
      secureLocalStorage.removeItem("token");
      secureLocalStorage.removeItem("authToken");
      setToken(null);
      setUser(null);
      setIsLoggedIn(false);
    }
  };

  useEffect(() => {
    console.log(" AuthContext: Initializing authentication check...");

    // Check token immediately without artificial delay
    const storedToken = secureLocalStorage.getItem("token");
    const authToken = secureLocalStorage.getItem("authToken");

    console.log(" AuthContext: Token found in storage:", !!storedToken);
    console.log(" AuthContext: AuthToken found in storage:", !!authToken);

    // Try both token keys for compatibility
    const tokenToUse = storedToken || authToken;

    if (tokenToUse && typeof tokenToUse === "string") {
      console.log(" AuthContext: Valid token found, checking validity...");
      try {
        checkTokenValidity(tokenToUse);
      } catch (error) {
        console.error(" AuthContext: Error checking token validity on mount:", error);
        // Don't logout on mount errors, just set loading to false
        setIsLoggedIn(false);
      }
    } else {
      console.log(" AuthContext: No token found in secure storage.");
      setIsLoggedIn(false);
    }

    console.log(" AuthContext: Authentication check complete, setting loading to false");
    setLoading(false); // Set loading to false after checking
  }, []); // Empty dependency array ensures this runs only once on mount

  // Clear cache when user changes (prevents cross-user data contamination)
  useEffect(() => {
    if (user?.id) {
      console.log(` User changed to ID: ${user.id}, clearing cache to prevent cross-user data`);
      // Only clear cache if this is not the initial load
      if (queryClient.getQueryCache().getAll().length > 0) {
        queryClient.clear();

        // Also clear browser cache for user isolation
        import('../utils/cacheManager').then(({ cacheManager }) => {
          cacheManager.clearBrowserCache();
        }).catch((error) => {
          console.warn('Could not clear browser cache:', error);
        });
      }
    }
  }, [user?.id]); // Trigger when user ID changes

  const login = (newToken: string) => {
    // Clear cache before setting new token to prevent cross-user data contamination
    queryClient.clear();

    secureLocalStorage.setItem("token", newToken);
    checkTokenValidity(newToken); // Validate the token upon login

    // Create audit log entry for login (after token validation)
    setTimeout(() => {
      const userData = extractUserFromToken(newToken);
      if (userData) {
        createAuditLogEntry('LOGIN', userData).catch(error => {
          console.warn(' Failed to create login audit log:', error);
        });
      }
    }, 100); // Small delay to ensure user state is set
  };

  return (
    <AuthContext.Provider value={{ isLoggedIn, token, user, login, logout, loading }}>
      {children}
    </AuthContext.Provider>
  );
};

// Custom hook to use the AuthContext
export const useAuth = (): AuthContextType => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth must be used within an AuthProvider");
  }
  return context;
};
