// src/App.tsx

import { Suspense, lazy, useEffect, useState } from "react";
import { Routes, Route, useLocation } from "react-router-dom";
import { Toaster } from "./components/ui/toaster";

// Layouts
const DashboardLayout = lazy(
  () => import("./layout/Dashboard/DashboardLayout")
);
import AuthLayout from "./layout/Auth/AuthLayout";

// Pages
const Login = lazy(() => import("./pages/auth/Login"));
const Register = lazy(() => import("./pages/auth/Register"));
const GmailCallback = lazy(() => import("./pages/auth/GmailCallback"));
const GmailPopupCallback = lazy(() => import("./pages/auth/GmailPopupCallback"));
const FacebookPopupCallback = lazy(() => import("./pages/auth/FacebookPopupCallback"));
const FacebookCallback = lazy(() => import("./pages/FacebookCallback"));

const Dashboard = lazy(() => import("./pages/dashboard/Dashboard"));
const KnowledgeBases = lazy(
  () => import("./pages/KnowledgeBases/KnowledgeBases")
);
const Action = lazy(() => import("./pages/Actions/Action"));
const Contacts = lazy(() => import("./pages/Contacts/Contacts"));
const Messages = lazy(() => import("./pages/Messages/Messages"));
// @ts-ignore
const Patients = lazy(() => import("./pages/Patients/Patients"));
// @ts-ignore
const ClinicalRoles = lazy(() => import("./pages/ClinicalRoles/ClinicalRoles"));
// @ts-ignore
const ClinicalDashboard = lazy(() => import("./pages/Clinical/ClinicalDashboard"));
// @ts-ignore
const ClinicalSites = lazy(() => import("./pages/Clinical/ClinicalSites"));
// @ts-ignore
const ClinicalStudies = lazy(() => import("./pages/Clinical/ClinicalStudies"));
// @ts-ignore
const ClinicalUsers = lazy(() => import("./pages/Clinical/ClinicalUsers"));
// @ts-ignore
const StudyParticipants = lazy(() => import("./pages/Clinical/StudyParticipants"));
const Lists = lazy(() => import("./pages/Lists/Lists"));
const ListDetails = lazy(() => import("./pages/Lists/ListDetails"));
const Campaigns = lazy(() => import("./pages/Campaigns/Campaigns"));
const Recordings = lazy(() => import("./pages/Recordings/Recordings"));
const RecordingDetail = lazy(() => import("./pages/Recordings/RecordingDetail"));
const Support = lazy(() => import("./pages/Support/Support"));
const SupportTicketDetail = lazy(() => import("./pages/Support/SupportTicketDetail").then(module => ({ default: module.SupportTicketDetail })));
const AdminSupport = lazy(() => import("./pages/Support/AdminSupport"));
const Agents = lazy(() => import("./pages/Agents/Agents"));
const VapiStyleAgents = lazy(() => import("./pages/Agents/VapiStyleAgents"));
const AddAgent = lazy(() => import("./pages/Agents/AddAgent").then(module => ({ default: module.AddAgent })));
const Models = lazy(() => import("./pages/Models/Models"));
const Voices = lazy(() => import("./pages/Voices/Voices"));
const VoicePreviewTest = lazy(() => import("./components/VoicePreviewTest"));
const PhoneNumbers = lazy(() => import("./pages/PhoneNumbers/PhoneNumbers"));
const OutboundCalls = lazy(() => import("./pages/OutboundCalls/OutboundCalls"));
const Outbound = lazy(() => import("./pages/Outbound/Outbound"));
const CreateCampaignPage = lazy(() => import("./pages/Outbound/CreateCampaignPage").then(module => ({ default: module.CreateCampaignPage })));
const Workflows = lazy(() => import("./pages/Workflows/Workflows"));
const WorkflowBuilder = lazy(() => import("./pages/Workflows/WorkflowBuilder"));
const Webhooks = lazy(() => import("./pages/Webhooks/Webhooks"));
const AuditLogs = lazy(() => import("./pages/AuditLogs/AuditLogs"));
const Integrations = lazy(() => import("./pages/Integrations/Integrations.jsx"));

// Role Management Pages
const SuperAdminDashboard = lazy(() => import("./pages/SuperAdmin/SuperAdminDashboard"));
const Roles = lazy(() => import("./pages/Roles/Roles"));
const RolePermissionManagement = lazy(() => import("./pages/Roles/RolePermissionManagement"));
const Permissions = lazy(() => import("./pages/Permissions/Permissions"));

// Default Assistant Templates
const DefaultAssistants = lazy(() => import("./pages/DefaultAssistants/DefaultAssistants"));

// User Management Pages
const UserManagement = lazy(() => import("./pages/Users/UserManagement"));

// Credit Management Pages
// @ts-ignore
const CreditManagement = lazy(() => import("./pages/Admin/CreditManagement"));
// @ts-ignore
const UserCreditPortal = lazy(() => import("./pages/Credits/UserCreditPortal"));
// @ts-ignore
const TestCreditsPage = lazy(() => import("./pages/Credits/TestCreditsPage"));
// @ts-ignore
const CreditPurchasePage = lazy(() => import("./pages/Credits/CreditPurchasePage"));
// @ts-ignore
const TestPurchasePage = lazy(() => import("./pages/TestPurchasePage"));
// @ts-ignore
const CreditDebug = lazy(() => import("./components/Debug/CreditDebug"));
// @ts-ignore
const SimpleCreditTest = lazy(() => import("./components/Debug/SimpleCreditTest"));
// @ts-ignore
const GmailDebug = lazy(() => import("./pages/Debug/GmailDebug"));
// @ts-ignore
const ModalTestPage = lazy(() => import("./pages/Credits/ModalTestPage"));

// @ts-ignore
const Settings = lazy(() => import("./pages/Settings/Settings.jsx"));
const TestLogout = lazy(() => import("./pages/TestLogout"));
const Privacy = lazy(() => import("./pages/Privacy"));
const Terms = lazy(() => import("./pages/Terms"));
const NotFound = lazy(() => import("./pages/NotFound/NotFound"));

// Protected Route
import ProtectedRoutes from "./components/ProtectedRoutes";

// Theme Provider
import { ThemeProvider } from "@/components/theme-provider";
import Loading from "./Loading";
import FastLoading from "./components/FastLoading";
import ConnectionStatus from "./components/ConnectionStatus";
import { AuthProvider } from "./context/AuthContext";
import { PermissionProvider } from "./context/PermissionContext";
import { MobileMenuProvider } from "./context/MobileMenuContext";

// Tanstack Query
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";

// Cache Manager
import { cacheManager } from "./utils/cacheManager";

// Performance Monitor
import { performanceMonitor } from "./utils/performanceMonitor";

// Browser Refresh Hook
import { useBrowserRefresh } from "./hooks/useBrowserRefresh";
import MarketingAds from "./marketingAds/MarketingAds";
import LeadsScraper from "./LeadsScraper/LeadsScraper";
import Leads from "./Leads/Leads";
import ImportLeads from "./Leads/ImportLeads";
import LeadDetail from "./Leads/LeadDetail";

import Studies_Calculator from "./Studies_analysis_Calculator/Studies_Calculator";
import StudyDetail from "./Studies_analysis_Calculator/StudyDetail.jsx";
import EmailManagement from "./EmailManagement/EmailManagement.jsx";

function App() {
  const location = useLocation();
  const [isInitialLoad, setIsInitialLoad] = useState(true);
  const { isRefresh, isOnline } = useBrowserRefresh();

  const queryClient = new QueryClient({
    defaultOptions: {
      queries: {
        staleTime: 30 * 1000, // 30 seconds (reduced from 1 minute)
        gcTime: 2 * 60 * 1000, // 2 minutes (reduced from 5 minutes)
        retry: 1,
        refetchOnWindowFocus: true, // Enable refetch on window focus
        refetchOnReconnect: true, // Refetch when reconnecting
        refetchInterval: 5 * 60 * 1000, // Auto-refetch every 5 minutes
        refetchIntervalInBackground: false, // Don't refetch in background
      },
      mutations: {
        onSuccess: () => {
          // Automatically invalidate related queries after successful mutations
          queryClient.invalidateQueries();
        },
      },
    },
  });

  // Initialize cache manager and performance monitor
  useEffect(() => {
    cacheManager.setQueryClient(queryClient);
    cacheManager.setupAutoCacheManagement();

    performanceMonitor.setupAutoMonitoring();

    console.log(' Cache management and performance monitoring initialized');

    // Mark initial load as complete after a short delay
    const timer = setTimeout(() => {
      setIsInitialLoad(false);
    }, 100);

    return () => clearTimeout(timer);
  }, [queryClient]);

  // Clear cache on route change (page navigation)
  useEffect(() => {
    if (!isInitialLoad && !isRefresh) {
      console.log(' Route changed, clearing cache for fresh data...');

      // Clear cache for navigation to ensure fresh data
      import('./utils/cacheManager').then(({ cacheManager }) => {
        cacheManager.clearCacheForNavigation();
        // Also invalidate page-specific queries
        cacheManager.invalidateQueriesByPage(location.pathname);
      }).catch((error) => {
        console.warn('Could not clear cache for navigation:', error);
      });
    }
  }, [location.pathname, isInitialLoad, isRefresh]);

  return (
    <>
      <QueryClientProvider client={queryClient}>
        <ThemeProvider>
          <AuthProvider>
            <PermissionProvider>
              <MobileMenuProvider>
                <Suspense fallback={<FastLoading delay={100} />}>
              <Routes>
                {/* Public Routes */}
                <Route path="privacy" element={<Privacy />} />
                <Route path="terms" element={<Terms />} />

                {/* Authentication Routes */}
                <Route element={<AuthLayout />}>
                  <Route path="login" element={<Login />} />
                  <Route path="register" element={<Register />} />
                  <Route path="gmail/callback" element={<GmailCallback />} />
                  <Route path="gmail/popup-callback" element={<GmailPopupCallback />} />
                  <Route path="facebook/popup-callback" element={<FacebookPopupCallback />} />
                  <Route path="integration/facebook/callback" element={<FacebookCallback />} />

                </Route>

                {/* Protected Routes */}
                <Route element={<ProtectedRoutes />}>
                  <Route path="/" element={<DashboardLayout />}>
                    <Route index element={<Dashboard />} />
                    <Route path="dashboard" element={<Dashboard />} />
                    <Route path="agents" element={<VapiStyleAgents />} />
                    <Route path="agents/list" element={<Agents />} />
                    <Route path="agents/add" element={<AddAgent />} />
                    <Route path="models" element={<Models />} />
                    <Route path="voices" element={<Voices />} />
                    <Route path="voice-test" element={<VoicePreviewTest />} />
                    <Route path="phone-numbers" element={<PhoneNumbers />} />
                    <Route path="outbound-calls" element={<OutboundCalls />} />
                    <Route path="outbound" element={<Outbound />} />
                    <Route path="outbound/create-campaign" element={<CreateCampaignPage />} />
                    <Route path="workflows" element={<Workflows />} />
                    <Route path="workflows/:id" element={<WorkflowBuilder />} />
                    <Route path="workflows/:id/edit" element={<WorkflowBuilder />} />
                    <Route path="dashboard/workflows" element={<Workflows />} />
                    <Route path="dashboard/workflows/:id" element={<WorkflowBuilder />} />
                    <Route path="dashboard/workflows/:id/edit" element={<WorkflowBuilder />} />
                    <Route path="webhooks" element={<Webhooks />} />
                    <Route path="credits" element={<UserCreditPortal />} />
                    <Route path="credits/purchase" element={<CreditPurchasePage />} />
                    <Route path="purchase-credits" element={<TestPurchasePage />} />
                    <Route path="buy-credits" element={<TestPurchasePage />} />
                    <Route path="test-purchase" element={<TestPurchasePage />} />
                    <Route path="test-credits" element={<TestCreditsPage />} />
                    <Route path="debug-credits" element={<CreditDebug />} />
                    <Route path="simple-credit-test" element={<SimpleCreditTest />} />
                    <Route path="debug-gmail" element={<GmailDebug />} />
                    <Route path="modal-test" element={<ModalTestPage />} />
                    <Route path="audit-logs" element={<AuditLogs />} />

                    <Route
                      path="knowledge-bases"
                      element={<KnowledgeBases />}
                    />
                    <Route path="actions" element={<Action />} />
                    <Route path="contacts" element={<Contacts />} />
                    <Route path="messages" element={<Messages />} />
                    <Route path="patients" element={<Patients />} />
                    <Route path="clinical-roles" element={<ClinicalRoles />} />
                    <Route path="clinical-dashboard" element={<ClinicalDashboard />} />
                    <Route path="clinical-users" element={<ClinicalUsers />} />
                    <Route path="clinical-sites" element={<ClinicalSites />} />
                    <Route path="clinical-studies" element={<ClinicalStudies />} />
                    <Route path="study-participants" element={<StudyParticipants />} />
                    <Route path="lists" element={<Lists />} />
                    <Route path="lists/:id" element={<ListDetails />} />
                    <Route path="campaigns" element={<Campaigns />} />
                    <Route path="recordings" element={<Recordings />} />
                    <Route path="recordings/:id" element={<RecordingDetail />} />

                    <Route path="support" element={<Support />} />
                    <Route path="support/ticket/:ticketId" element={<SupportTicketDetail />} />
                    <Route path="admin-support" element={<AdminSupport />} />

                    {/* User Management Routes */}
                    <Route path="users" element={<UserManagement />} />
                    <Route path="marketing-ads" element={<MarketingAds />} />
                    <Route path="leads-scraper" element={<LeadsScraper />} />
                    <Route path="leads-contacts" element={<Leads />} />
                    <Route path="leads-contacts/import" element={<ImportLeads />} />
                    <Route path="leads/:id" element={<LeadDetail />} />
                    <Route path="integration" element={<Integrations />} />

                    {/* Role Management Routes */}
                    <Route path="super-admin" element={<SuperAdminDashboard />} />
                    <Route path="roles" element={<RolePermissionManagement />} />
                    <Route path="roles/old" element={<Roles />} />
                    <Route path="permissions" element={<Permissions />} />

                    {/* Default Assistant Templates */}
                    <Route path="default-assistants" element={<DefaultAssistants />} />

                    {/* Credit Management Routes */}
                    <Route path="admin/credits" element={<CreditManagement />} />

                    <Route path="settings" element={<Settings />} />
                    <Route path="studies_calculator" element={<Studies_Calculator />} />
                    <Route path="/studies/db/:id" element={<StudyDetail />} />

                    <Route path="email_management" element={<EmailManagement />} />
                  </Route>
                </Route>

               

                {/* Test Route Outside Protected Routes */}
                <Route path="/test-route-direct" element={<TestPurchasePage />} />

                {/* Fallback Routes */}
                <Route path="*" element={<NotFound />} />
              </Routes>
              </Suspense>
              </MobileMenuProvider>
            </PermissionProvider>
          </AuthProvider>
        </ThemeProvider>
        <Toaster richColors position="top-right" />
        <ConnectionStatus />
      </QueryClientProvider>
    </>
  );
}

export default App;
