import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axiosInstance from "@/api/axios/axiosInstance";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
import L from "leaflet";

// Helpers
const safeJoin = (arr, sep = ", ") =>
  Array.isArray(arr) && arr.length ? arr.join(sep) : "";

const StatusBadge = ({ status }) => {
  if (!status) return null;

  const map = {
    RECRUITING: "bg-green-100 text-green-800 border-green-200",
    COMPLETED: "bg-blue-100 text-blue-800 border-blue-200",
    ACTIVE_NOT_RECRUITING: "bg-indigo-100 text-indigo-800 border-indigo-200",
    NOT_YET_RECRUITING: "bg-yellow-100 text-yellow-800 border-yellow-200",
    TERMINATED: "bg-red-100 text-red-800 border-red-200",
    WITHDRAWN: "bg-red-100 text-red-800 border-red-200",
    SUSPENDED: "bg-orange-100 text-orange-800 border-orange-200",
  };

  const cls = map[status] || "bg-gray-100 text-gray-700 border-gray-200";

  return (
    <span
      className={`inline-flex items-center px-3 py-1 rounded-full text-xs border ${cls}`}
    >
      {status}
    </span>
  );
};

const SectionCard = ({ title, children }) => {
  if (!children) return null;
  return (
    <div className="bg-white rounded-lg shadow border border-gray-100">
      <div className="px-6 py-4 border-b bg-gray-50 rounded-t-lg">
        <h2 className="text-base font-semibold text-gray-900">{title}</h2>
      </div>
      <div className="p-6">{children}</div>
    </div>
  );
};

const KeyValueGrid = ({ items }) => {
  const filtered = items.filter(
    (it) => it.value !== undefined && it.value !== null && it.value !== ""
  );
  if (!filtered.length) return null;

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
      {filtered.map((it) => (
        <div
          key={it.label}
          className="bg-gray-50 border border-gray-100 rounded-lg p-4"
        >
          <p className="text-gray-500">{it.label}</p>
          <p className="font-medium text-gray-900 mt-1">{it.value || ""}</p>
        </div>
      ))}
    </div>
  );
};

delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl:
    "https://unpkg.com/leaflet@1.9.4/dist/images/marker-icon-2x.png",
  iconUrl: "https://unpkg.com/leaflet@1.9.4/dist/images/marker-icon.png",
  shadowUrl: "https://unpkg.com/leaflet@1.9.4/dist/images/marker-shadow.png",
});

// Group locations by country (CT.gov logic)
const groupByCountry = (locations = []) =>
  locations.reduce((acc, loc) => {
    const country = loc.country || "Unknown";
    if (!acc[country]) acc[country] = [];
    acc[country].push(loc);
    return acc;
  }, {});

const LocationsBlock = ({ locations }) => {
  if (!Array.isArray(locations) || locations.length === 0) return null;

  const grouped = groupByCountry(locations);
  const firstGeo = locations.find((l) => l.geoPoint)?.geoPoint;

  return (
    <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
      {/* LEFT SIDE  CT.gov style list */}
      <div className="space-y-10">
        {Object.entries(grouped).map(([country, locs]) => (
          <div key={country} className="space-y-5">
            {/* Country Header */}
            <h3 className="text-base font-semibold text-gray-900 border-b pb-2">
              {country}
            </h3>

            <div className="space-y-4">
              {locs.map((loc, idx) => (
                <div
                  key={idx}
                  className="relative bg-white rounded-lg border border-gray-200 
                       p-4 pl-5 shadow-sm hover:shadow-md transition"
                >
                  {/* Left Accent Bar */}
                  <span className="absolute left-0 top-0 h-full w-1 rounded-l-lg bg-blue-600" />

                  {/* City + Status */}
                  <div className="flex items-center gap-2 mb-1">
                    <span className="text-sm font-semibold text-gray-900">
                       {loc.city || ""}
                    </span>

                    {loc.status && (
                      <span
                        className={`px-2 py-0.5 text-xs rounded-full border font-medium
                    ${
                      loc.status === "RECRUITING"
                        ? "bg-green-100 text-green-800 border-green-200"
                        : "bg-gray-100 text-gray-700 border-gray-200"
                    }`}
                      >
                        {loc.status}
                      </span>
                    )}
                  </div>

                  {/* Facility */}
                  <p className="text-sm text-gray-800 font-medium">
                    {loc.facility || ""}
                  </p>

                  {/* Contacts */}
                  {Array.isArray(loc.contacts) && loc.contacts.length > 0 && (
                    <div className="mt-2 space-y-1 text-xs text-gray-600">
                      {loc.contacts.map((c, i) => (
                        <div key={i}>
                          <span className="font-medium text-gray-800">
                            {c.name || "Contact"}
                          </span>
                          {c.phone && <>  {c.phone}</>}
                          {c.email && (
                            <>
                              {" "}
                              {" "}
                              <a
                                href={`mailto:${c.email}`}
                                className="text-blue-600 hover:underline"
                              >
                                {c.email}
                              </a>
                            </>
                          )}
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>

      {/* RIGHT SIDE  MAP WITH PINS */}
      <div className="h-[380px] rounded border overflow-hidden">
        {firstGeo ? (
          <MapContainer
            center={[firstGeo.lat, firstGeo.lon]}
            zoom={4}
            style={{ height: "100%", width: "100%" }}
          >
            <TileLayer
              attribution="&copy; OpenStreetMap"
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />

            {locations
              .filter((l) => l.geoPoint)
              .map((loc, i) => (
                <Marker key={i} position={[loc.geoPoint.lat, loc.geoPoint.lon]}>
                  <Popup>
                    <strong>{loc.facility}</strong>
                    <br />
                    {loc.city}, {loc.country}
                    <br />
                    Status: {loc.status}
                  </Popup>
                </Marker>
              ))}
          </MapContainer>
        ) : (
          <div className="flex items-center justify-center h-full text-gray-500">
            No map data available
          </div>
        )}
      </div>
    </div>
  );
};

const CentralContactsBlock = ({ centralContacts }) => {
  if (!Array.isArray(centralContacts) || centralContacts.length === 0)
    return null;

  return (
    <div className="space-y-3">
      {centralContacts.map((c, idx) => (
        <div key={idx} className="border border-gray-100 rounded-lg p-4">
          <p className="font-semibold text-gray-900">{c.name || ""}</p>
          <p className="text-sm text-gray-600 mt-1">{c.role || ""}</p>
          <div className="text-sm text-gray-700 mt-2 space-y-1">
            {c.email ? (
              <div>
                <span className="text-gray-500">Email:</span> {c.email}
              </div>
            ) : null}
            {c.phone ? (
              <div>
                <span className="text-gray-500">Phone:</span> {c.phone}
              </div>
            ) : null}
          </div>
        </div>
      ))}
    </div>
  );
};

const SponsorsBlock = ({ sponsorModule }) => {
  if (!sponsorModule) return null;

  const lead = sponsorModule.leadSponsor?.name;
  const collabs = sponsorModule.collaborators || [];
  const rp = sponsorModule.responsibleParty || null;

  return (
    <div className="space-y-4 text-sm">
      {lead ? (
        <div className="border border-gray-100 rounded-lg p-4">
          <p className="text-gray-500">Lead Sponsor</p>
          <p className="font-semibold text-gray-900 mt-1">{lead}</p>
        </div>
      ) : null}

      {Array.isArray(collabs) && collabs.length > 0 ? (
        <div className="border border-gray-100 rounded-lg p-4">
          <p className="text-gray-500">Collaborators</p>
          <ul className="mt-2 list-disc ml-5 text-gray-800 space-y-1">
            {collabs.map((c, idx) => (
              <li key={idx}>{c.name}</li>
            ))}
          </ul>
        </div>
      ) : null}

      {rp ? (
        <div className="border border-gray-100 rounded-lg p-4">
          <p className="text-gray-500">Responsible Party</p>
          <div className="mt-2 space-y-1">
            <div>
              <span className="text-gray-500">Type:</span> {rp.type || ""}
            </div>
            {rp.investigatorFullName ? (
              <div>
                <span className="text-gray-500">Investigator:</span>{" "}
                {rp.investigatorFullName}
              </div>
            ) : null}
            {rp.investigatorTitle ? (
              <div>
                <span className="text-gray-500">Title:</span>{" "}
                {rp.investigatorTitle}
              </div>
            ) : null}
            {rp.investigatorAffiliation ? (
              <div>
                <span className="text-gray-500">Affiliation:</span>{" "}
                {rp.investigatorAffiliation}
              </div>
            ) : null}
          </div>
        </div>
      ) : null}
    </div>
  );
};

const OutcomeTable = ({ title, outcomes }) => {
  if (!Array.isArray(outcomes) || outcomes.length === 0) return null;

  return (
    <div className="space-y-3">
      {/* Sub-section title (Primary / Secondary) */}
      <h3 className="text-sm font-semibold text-gray-800 border-b border-blue-200 pb-2">
        {title}
      </h3>

      {/* Table wrapper */}
      <div className="overflow-x-auto">
        <table className="w-full text-sm border border-gray-400 border-collapse">
          {/* Header */}
          <thead className="bg-gray-100">
            <tr>
              <th className="border border-gray-400 px-3 py-2 text-left font-semibold text-gray-900 w-1/4">
                Outcome Measure
              </th>
              <th className="border border-gray-400 px-3 py-2 text-left font-semibold text-gray-900">
                Measure Description
              </th>
              <th className="border border-gray-400 px-3 py-2 text-left font-semibold text-gray-900 w-1/4">
                Time Frame
              </th>
            </tr>
          </thead>

          {/* Body */}
          <tbody>
            {outcomes.map((o, idx) => (
              <tr key={idx} className="bg-white">
                <td className="border border-gray-400 px-3 py-3 align-top font-medium text-gray-900 whitespace-pre-wrap">
                  {o.measure || ""}
                </td>

                <td className="border border-gray-400 px-3 py-3 align-top text-gray-800 whitespace-pre-wrap">
                  {o.description || ""}
                </td>

                <td className="border border-gray-400 px-3 py-3 align-top text-gray-800 whitespace-pre-wrap">
                  {o.timeFrame || ""}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

const ArmsInterventionsTable = ({ armsModule }) => {
  if (!armsModule?.armGroups?.length) return null;

  const armGroups = armsModule.armGroups || [];
  const interventions = armsModule.interventions || [];

  // Map interventions by arm label
  const interventionsByArm = {};

  interventions.forEach((intv) => {
    if (!Array.isArray(intv.armGroupLabels)) return;

    intv.armGroupLabels.forEach((label) => {
      if (!interventionsByArm[label]) {
        interventionsByArm[label] = [];
      }
      interventionsByArm[label].push(intv);
    });
  });
  return (
    <div className="space-y-4">
      {/* Section title */}
      <h3 className="text-sm font-semibold text-gray-900 border-b pb-2">
        Arms and Interventions
      </h3>

      {/* Table */}
      <div className="overflow-x-auto">
        <table className="w-full text-sm border border-gray-400 border-collapse">
          {/* Header */}
          <thead className="bg-gray-100">
            <tr>
              <th className="border border-gray-400 px-4 py-2 text-left font-semibold w-1/3">
                Participant Group / Arm
              </th>
              <th className="border border-gray-400 px-4 py-2 text-left font-semibold">
                Intervention / Treatment
              </th>
            </tr>
          </thead>

          {/* Body */}
          <tbody>
            {armGroups.map((arm, idx) => (
              <tr key={idx} className="align-top">
                {/* LEFT COLUMN  ARM */}
                <td className="border border-gray-400 px-4 py-3">
                  <p className="font-semibold text-gray-900">
                    {arm.type === "EXPERIMENTAL"
                      ? "Experimental:"
                      : "Comparator:"}{" "}
                    {arm.label}
                  </p>
                  {arm.description && (
                    <p className="mt-2 text-gray-700 whitespace-pre-line">
                      {arm.description}
                    </p>
                  )}
                </td>

                {/* RIGHT COLUMN  INTERVENTIONS */}
                <td className="border border-gray-400 px-4 py-3 space-y-4">
                  {(interventionsByArm[arm.label] || []).map((intv, i) => (
                    <div key={i}>
                      <p className="font-semibold text-gray-900">
                        {intv.type.replace(/_/g, " ")}:{" "}
                        <span className="bg-yellow-200 px-1">{intv.name}</span>
                      </p>
                      {intv.description && (
                        <ul className="list-disc ml-5 mt-1 text-gray-700">
                          <li>{intv.description}</li>
                        </ul>
                      )}
                    </div>
                  ))}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

const StudyDetail = () => {
  const { id } = useParams();
  const [study, setStudy] = useState(null);

  useEffect(() => {
    axiosInstance
      .get(`/api/studies/db/${id}`)
      .then((res) => setStudy(res.data.study.raw_json))
      .catch(() => setStudy(null));
  }, [id]);

  if (!study) return <p className="p-6">Loading</p>;

  const ps = study.protocolSection || {};
  const idm = ps.identificationModule || {};
  const sm = ps.statusModule || {};
  const dm = ps.designModule || {};
  const desc = ps.descriptionModule || {};
  const cm = ps.conditionsModule || {};
  const om = ps.outcomesModule || {};
  const em = ps.eligibilityModule || {};
  const clm = ps.contactsLocationsModule || {};
  const scm = ps.sponsorCollaboratorsModule || {};

  // Completion Date fallback like CT.gov sometimes does
  const completionDate =
    sm.completionDateStruct?.date ||
    sm.primaryCompletionDateStruct?.date ||
    "";

  const OverviewItem = ({ label, value }) => (
    <div className="border-b pb-2">
      <p className="text-xs font-semibold text-gray-600">{label}</p>
      <p className="text-sm text-gray-900 mt-1">{value || ""}</p>
    </div>
  );

  const StudyRecordDates = ({ status }) => {
    if (!status) return null;

    return (
      <div className="bg-white rounded-lg border border-gray-200">
        {/* Header */}
        <div className="px-6 py-4 border-b">
          <h2 className="text-lg font-semibold text-gray-900">
            Study Record Dates
          </h2>
          <p className="text-sm text-gray-700 mt-1">
            These dates track the progress of study record and summary results
            submissions to ClinicalTrials.gov. Study records and reported
            results are reviewed by the National Library of Medicine (NLM) to
            make sure they meet specific quality control standards before being
            posted on the public website.
          </p>
        </div>

        {/* Two-column layout */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 px-6 py-6 text-sm">
          {/* LEFT  Study Registration Dates */}
          <div className="border rounded-md">
            <div className="bg-gray-100 px-4 py-2 font-semibold text-gray-900">
              Study Registration Dates
            </div>

            <div className="px-4 py-3 space-y-4">
              <div>
                <p className="font-semibold text-gray-900">First Submitted</p>
                <p className="text-gray-800">
                  {status.studyFirstSubmitDate || ""}
                </p>
              </div>

              <div>
                <p className="font-semibold text-gray-900">
                  First Submitted that Met QC Criteria
                </p>
                <p className="text-gray-800">
                  {status.studyFirstSubmitQcDate || ""}
                </p>
              </div>

              <div>
                <p className="font-semibold text-gray-900">
                  First Posted (Estimated)
                </p>
                <p className="text-gray-800">
                  {status.studyFirstPostDateStruct?.date || ""}
                </p>
              </div>
            </div>
          </div>

          {/* RIGHT  Study Record Updates */}
          <div className="border rounded-md">
            <div className="bg-gray-100 px-4 py-2 font-semibold text-gray-900">
              Study Record Updates
            </div>

            <div className="px-4 py-3 space-y-4">
              <div>
                <p className="font-semibold text-gray-900">
                  Last Update Submitted that Met QC Criteria
                </p>
                <p className="text-gray-800">
                  {status.lastUpdateSubmitDate || ""}
                </p>
              </div>

              <div>
                <p className="font-semibold text-gray-900">
                  Last Update Posted (Estimated)
                </p>
                <p className="text-gray-800">
                  {status.lastUpdatePostDateStruct?.date || ""}
                </p>
              </div>

              <div>
                <p className="font-semibold text-gray-900">Last Verified</p>
                <p className="text-gray-800">
                  {status.statusVerifiedDate || ""}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };

  const TermsRelatedBlock = ({ conditionsModule, conditionBrowseModule }) => {
    const keywords = conditionsModule?.keywords || [];
    const meshes = conditionBrowseModule?.meshes || [];
    const ancestors = conditionBrowseModule?.ancestors || [];

    if (!keywords.length && !meshes.length && !ancestors.length) return null;

    return (
      <div className="bg-white rounded-lg border border-gray-200">
        {/* Header */}
        <div className="bg-blue-50 px-6 py-3 flex items-center justify-between">
          <h2 className="text-base font-semibold text-blue-900">
            Terms related to this study
          </h2>
          <span className="text-xl font-semibold text-blue-900"></span>
        </div>

        {/* Content */}
        <div className="px-6 py-6 grid grid-cols-1 md:grid-cols-2 gap-10 text-sm">
          {/* LEFT COLUMN  Keywords */}
          <div>
            <h3 className="font-semibold text-gray-700 border-b pb-2 mb-3">
              Keywords Provided by Study Sponsor
            </h3>

            {keywords.length ? (
              <ul className="space-y-1 text-gray-900">
                {keywords.map((k, i) => (
                  <li key={i}>{k}</li>
                ))}
              </ul>
            ) : (
              <p className="text-gray-500"></p>
            )}
          </div>

          {/* RIGHT COLUMN  MeSH Terms */}
          <div>
            <h3 className="font-semibold text-gray-700 border-b pb-2 mb-3">
              Additional Relevant MeSH Terms
            </h3>

            {[...meshes, ...ancestors].length ? (
              <ul className="space-y-1 text-gray-900">
                {[...meshes, ...ancestors].map((m, i) => (
                  <li key={i}>{m.term}</li>
                ))}
              </ul>
            ) : (
              <p className="text-gray-500"></p>
            )}
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className="bg-gray-50 min-h-screen p-6 space-y-6">
      {/* Header (CT.gov style) */}
      <div className="bg-white px-6 py-5 border-b space-y-4">
        {/* Recruitment Status */}
        {sm.overallStatus && (
          <div>
            <span
              className={`inline-flex items-center px-3 py-1 rounded text-xs font-semibold
          ${
            sm.overallStatus === "RECRUITING"
              ? "bg-green-600 text-white"
              : "bg-gray-600 text-white"
          }`}
            >
              {sm.overallStatus}
            </span>
          </div>
        )}

        {/* Study Title */}
        <h1 className="text-xl font-bold text-gray-900 leading-snug">
          <span className="bg-yellow-200 px-1">{idm.briefTitle || ""}</span>
        </h1>

        {/* Meta Information */}
        <div className="space-y-2 text-sm">
          {/* ClinicalTrials.gov ID */}
          <div>
            <span className="text-blue-700 font-semibold">NCT ID</span>{" "}
            <span className="text-gray-900">{idm.nctId || ""}</span>
          </div>

          {/* Sponsor */}
          {scm.leadSponsor?.name && (
            <div>
              <span className="text-blue-700 font-semibold">Sponsor</span>{" "}
              <span className="text-gray-900">{scm.leadSponsor.name}</span>
            </div>
          )}

          {/* Responsible Party */}
          {scm.responsibleParty?.organization && (
            <div>
              <span className="text-blue-700 font-semibold">
                Information provided by
              </span>{" "}
              <span className="text-gray-900">
                {scm.responsibleParty.organization}
              </span>
            </div>
          )}

          {/* Last Update */}
          {sm.lastUpdatePostDateStruct?.date && (
            <div>
              <span className="text-blue-700 font-semibold">
                Last Update Posted
              </span>{" "}
              <span className="text-gray-900">
                {sm.lastUpdatePostDateStruct.date}
              </span>
            </div>
          )}
        </div>
      </div>

      {/*  Overview (Study Type / Phase / Enrollment / Dates) */}
      <SectionCard title="Study Overview">
        <div className="bg-white px-6 py-4">
          {/* Title */}
          <h2 className="text-lg font-semibold text-gray-900 mb-4">
            Study Overview
          </h2>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-10">
            {/* LEFT COLUMN */}
            <div className="lg:col-span-2 text-sm text-gray-800 space-y-6">
              {/* Brief Summary */}
              {desc.briefSummary && (
                <div>
                  <p className="font-semibold text-gray-900 border-b pb-1 mb-2">
                    Brief Summary
                  </p>
                  <p className="leading-relaxed whitespace-pre-line">
                    {desc.briefSummary}
                  </p>
                </div>
              )}

              {/* Official Title */}
              {idm.officialTitle && (
                <div>
                  <p className="font-semibold text-gray-900 border-b pb-1 mb-2">
                    Official Title
                  </p>
                  <p className="leading-relaxed whitespace-pre-line">
                    {idm.officialTitle}
                  </p>
                </div>
              )}

              {/* Conditions */}
              {Array.isArray(cm.conditions) && cm.conditions.length > 0 && (
                <div>
                  <p className="font-semibold text-gray-900 border-b pb-1 mb-2">
                    Conditions
                  </p>
                  <div className="flex flex-wrap gap-2">
                    {cm.conditions.map((c, i) => (
                      <span
                        key={i}
                        className="px-2 py-0.5 text-xs rounded bg-blue-100 text-blue-800"
                      >
                        {c}
                      </span>
                    ))}
                  </div>
                </div>
              )}

              {/* Intervention / Treatment */}
              {ps.armsInterventionsModule?.interventions?.length > 0 && (
                <div>
                  <p className="font-semibold text-gray-900 border-b pb-1 mb-2">
                    Intervention / Treatment
                  </p>
                  <ul className="list-disc ml-5 space-y-1">
                    {ps.armsInterventionsModule.interventions.map((i, idx) => (
                      <li key={idx}>
                        {i.type}: <strong>{i.name}</strong>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>

            {/* RIGHT COLUMN */}
            <div className="text-sm space-y-4">
              <OverviewItem
                label="Study Start (Actual)"
                value={sm.startDateStruct?.date}
              />
              <OverviewItem
                label="Primary Completion (Estimated)"
                value={sm.primaryCompletionDateStruct?.date}
              />
              <OverviewItem
                label="Study Completion (Estimated)"
                value={completionDate}
              />
              <OverviewItem
                label="Enrollment (Estimated)"
                value={dm.enrollmentInfo?.count}
              />
              <OverviewItem label="Study Type" value={dm.studyType} />
              <OverviewItem label="Phase" value={safeJoin(dm.phases)} />
            </div>
          </div>
        </div>
      </SectionCard>

      {/*  Brief + Detailed Description */}
      <SectionCard title="Study Description">
        <div className="space-y-4">
          {desc.briefSummary ? (
            <div>
              <p className="text-sm font-semibold text-gray-900">
                Brief Summary
              </p>
              <p className="text-sm text-gray-700 mt-1 whitespace-pre-line">
                {desc.briefSummary}
              </p>
            </div>
          ) : null}

          {desc.detailedDescription ? (
            <div>
              <p className="text-sm font-semibold text-gray-900">
                Detailed Description
              </p>
              <p className="text-sm text-gray-700 mt-1 whitespace-pre-line">
                {desc.detailedDescription}
              </p>
            </div>
          ) : null}

          {!desc.briefSummary && !desc.detailedDescription ? (
            <p className="text-sm text-gray-500"></p>
          ) : null}
        </div>
      </SectionCard>

      {/*  Conditions + Keywords */}
      <SectionCard title="Conditions and Keywords">
        <div className="space-y-4">
          <div>
            <p className="text-sm font-semibold text-gray-900">Conditions</p>
            <div className="mt-2 flex flex-wrap gap-2">
              {Array.isArray(cm.conditions) && cm.conditions.length ? (
                cm.conditions.map((c, i) => (
                  <span
                    key={i}
                    className="px-3 py-1 rounded-full text-xs bg-blue-50 text-blue-700 border border-blue-100"
                  >
                    {c}
                  </span>
                ))
              ) : (
                <span className="text-sm text-gray-500"></span>
              )}
            </div>
          </div>

          <div>
            <p className="text-sm font-semibold text-gray-900">Keywords</p>
            <div className="mt-2 flex flex-wrap gap-2">
              {Array.isArray(cm.keywords) && cm.keywords.length ? (
                cm.keywords.map((k, i) => (
                  <span
                    key={i}
                    className="px-3 py-1 rounded-full text-xs bg-gray-50 text-gray-700 border border-gray-200"
                  >
                    {k}
                  </span>
                ))
              ) : (
                <span className="text-sm text-gray-500"></span>
              )}
            </div>
          </div>
        </div>
      </SectionCard>

      {/*  Outcomes (Primary + Secondary) */}
      {(om.primaryOutcomes?.length || om.secondaryOutcomes?.length) && (
        <SectionCard title="What is the study measuring?">
          <div className="space-y-8">
            <OutcomeTable
              title="Primary Outcome Measures"
              outcomes={om.primaryOutcomes}
            />

            <OutcomeTable
              title="Secondary Outcome Measures"
              outcomes={om.secondaryOutcomes}
            />
          </div>
        </SectionCard>
      )}

      {/*  Locations */}
      {Array.isArray(clm.locations) && clm.locations.length ? (
        <SectionCard title="Locations">
          <LocationsBlock locations={clm.locations} />
        </SectionCard>
      ) : null}

      {/*  Contacts (centralContacts) */}
      {Array.isArray(clm.centralContacts) && clm.centralContacts.length ? (
        <SectionCard title="Central Contacts">
          <CentralContactsBlock centralContacts={clm.centralContacts} />
        </SectionCard>
      ) : null}

      {/*  Eligibility */}
      <SectionCard title="Eligibility Criteria">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* LEFT  Inclusion / Exclusion */}
          <div className="lg:col-span-2 space-y-4">
            <h3 className="text-sm font-semibold text-gray-900 border-b pb-2">
              Description
            </h3>

            {em.eligibilityCriteria ? (
              <div className="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">
                {em.eligibilityCriteria}
              </div>
            ) : (
              <p className="text-sm text-gray-500"></p>
            )}
          </div>

          {/* RIGHT  Summary Box */}
          <div className="space-y-4">
            {/* Age */}
            <div className="bg-gray-50 border rounded-lg p-4">
              <p className="text-xs text-gray-500 flex items-center gap-1">
                Ages Eligible for Study
                <span className="cursor-help"></span>
              </p>
              <p className="text-sm font-semibold text-gray-900 mt-1">
                {em.minimumAge || ""}
                {em.maximumAge ? ` to ${em.maximumAge}` : ""}
                {em.minimumAge ? " (Adult)" : ""}
              </p>
            </div>

            {/* Sex */}
            <div className="bg-gray-50 border rounded-lg p-4">
              <p className="text-xs text-gray-500 flex items-center gap-1">
                Sexes Eligible for Study
                <span className="cursor-help"></span>
              </p>
              <p className="text-sm font-semibold text-gray-900 mt-1">
                {em.sex || ""}
              </p>
            </div>

            {/* Healthy Volunteers */}
            <div className="bg-gray-50 border rounded-lg p-4">
              <p className="text-xs text-gray-500 flex items-center gap-1">
                Accepts Healthy Volunteers
                <span className="cursor-help"></span>
              </p>
              <p className="text-sm font-semibold text-gray-900 mt-1">
                {em.healthyVolunteers === true
                  ? "Yes"
                  : em.healthyVolunteers === false
                  ? "No"
                  : ""}
              </p>
            </div>
          </div>
        </div>
      </SectionCard>

      {/* Study Plan */}
      {ps.armsInterventionsModule && (
        <div className="bg-white rounded-lg border border-gray-200">
          {/* Study Plan Header */}
          <div className="px-6 py-4 border-b">
            <h2 className="text-lg font-semibold text-gray-900">Study Plan</h2>
            <p className="text-sm text-gray-700 mt-1">
              This section provides details of the study plan, including how the
              study is designed and what the study is measuring.
            </p>
          </div>

          {/* How is the study designed */}
          <div className="bg-blue-50 px-6 py-3 flex items-center justify-between">
            <h3 className="text-base font-semibold text-blue-900">
              How is the study designed?
            </h3>
            <span className="text-xl font-semibold text-blue-900"></span>
          </div>

          <div className="px-6 py-5 space-y-4">
            {/* Design Details */}
            <div>
              <h4 className="text-sm font-semibold text-gray-900 mb-3">
                Design Details
              </h4>

              <div className="space-y-2 text-sm text-gray-900">
                <div>
                  <strong>Primary Purpose:</strong>{" "}
                  {dm.designInfo?.primaryPurpose
                    ? dm.designInfo.primaryPurpose.replace(/_/g, " ")
                    : ""}
                </div>

                <div>
                  <strong>Allocation:</strong>{" "}
                  {dm.designInfo?.allocation || "N/A"}
                </div>

                <div>
                  <strong>Interventional Model:</strong>{" "}
                  {dm.designInfo?.interventionModel
                    ? dm.designInfo.interventionModel.replace(/_/g, " ")
                    : ""}
                </div>

                <div>
                  <strong>Masking:</strong>{" "}
                  {dm.designInfo?.maskingInfo?.masking
                    ? `${dm.designInfo.maskingInfo.masking.replace(/_/g, " ")} `
                    : ""}
                </div>
              </div>
            </div>

            {/* Divider */}
            <hr className="border-t border-blue-200" />

            {/* Arms and Interventions */}
            <ArmsInterventionsTable armsModule={ps.armsInterventionsModule} />
          </div>
        </div>
      )}

      {/* Study Details */}
      <StudyRecordDates status={sm} />

      {/* Terms related to this study */}
      {(cm || study.derivedSection?.conditionBrowseModule) && (
        <TermsRelatedBlock
          conditionsModule={cm}
          conditionBrowseModule={study.derivedSection?.conditionBrowseModule}
        />
      )}

      {/*  Sponsors (full module) */}
      {scm && Object.keys(scm).length ? (
        <SectionCard title="Sponsors and Collaborators">
          <SponsorsBlock sponsorModule={scm} />
        </SectionCard>
      ) : null}
    </div>
  );
};

export default StudyDetail;
