import React, { useEffect, useRef, useState } from "react";
import axiosInstance from "@/api/axios/axiosInstance";

const EmailManagement = () => {
  const [emailTypes, setEmailTypes] = useState([]);
  const [frequencies, setFrequencies] = useState([]);
  const [users, setUsers] = useState([]);
  const [rules, setRules] = useState([]);

  const [selectedEmailType, setSelectedEmailType] = useState(null);
  const [selectedUser, setSelectedUser] = useState("");
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [selectedType, setSelectedType] = useState(null);

  const [phases, setPhases] = useState([]);
  const [conditions, setConditions] = useState([]);

  const [selectedPhases, setSelectedPhases] = useState([]);
  const [selectedConditions, setSelectedConditions] = useState([]);

  const [editingRule, setEditingRule] = useState(null);

  const [form, setForm] = useState({
    rule_name: "",
    location_text: "",
    condition_text: "",
    frequency_code: "",
  });

  useEffect(() => {
    axiosInstance
      .get("/api/email-management/email-types")
      .then((r) => setEmailTypes(r.data.data));

    axiosInstance
      .get("/api/email-management/frequencies")
      .then((r) => setFrequencies(r.data.data));

    axiosInstance
      .get("/api/email-management/users")
      .then((r) => setUsers(r.data.data));

    axiosInstance
      .get("/api/email-management/study-phases")
      .then((r) => setPhases(r.data.data));

    axiosInstance
      .get("/api/email-management/study-conditions")
      .then((r) => setConditions(r.data.data));
  }, []);

  const openStudyDrawer = (type) => {
    if (type.code !== "STUDY") return;
    setSelectedEmailType(type);
    setDrawerOpen(true);
  };

  const fetchRules = (userId) => {
    axiosInstance
      .get(`/api/email-management/rules/${userId}`)
      .then((r) => setRules(r.data.data));
  };

  const saveRule = async () => {
    if (!selectedUser) {
      alert("Please select a user first");
      return;
    }

    if (editingRule) {
      //  UPDATE
      await axiosInstance.put(`/api/email-management/rules/${editingRule.id}`, {
        location_text: form.location_text || null,
        phase: selectedPhases,
        condition_text: selectedConditions,
        frequency_code: form.frequency_code,
        timezone: "America/Chicago",
        schedule_day_of_week: form.schedule_day_of_week,
        schedule_day_of_month: form.schedule_day_of_month,
        schedule_time: form.schedule_time,
        is_enabled: 1,
      });
    } else {
      //  CREATE
      await axiosInstance.post("/api/email-management/rules", {
        email_type_code: "STUDY",
        target_user_id: selectedUser,
        ...form,
      });
    }

    //  RESET STATE
    setEditingRule(null);
    setForm({
      location_text: "",
      condition_text: "",
      frequency_code: "",
    });
    setSelectedPhases([]);
    setSelectedConditions([]);

    fetchRules(selectedUser);
  };

  const deleteRule = async (id) => {
    if (!confirm("Are you sure you want to delete this rule?")) return;

    await axiosInstance.delete(`/api/email-management/rules/${id}`);
    fetchRules(selectedUser);
  };

  const parseJSON = (val) => {
    try {
      return val ? JSON.parse(val) : [];
    } catch {
      return [];
    }
  };

  const formatFrequency = (r) => {
    if (r.frequency_code === "DAILY") return "Daily";
    if (r.frequency_code === "WEEKLY")
      return `Weekly (Day ${r.schedule_day_of_week})`;
    if (r.frequency_code === "MONTHLY")
      return `Monthly (Day ${r.schedule_day_of_month})`;
    return r.frequency_code;
  };

  const getOrdinal = (n) => {
    if (n > 3 && n < 21) return "th";
    switch (n % 10) {
      case 1:
        return "st";
      case 2:
        return "nd";
      case 3:
        return "rd";
      default:
        return "th";
    }
  };

  useEffect(() => {
    const close = () => setOpen(false);
    document.addEventListener("click", close);
    return () => document.removeEventListener("click", close);
  }, []);

  const SearchableMultiSelect = ({
    label,
    options = [],
    value = [],
    onChange,
    placeholder = "Select...",
  }) => {
    const [open, setOpen] = useState(false);
    const [search, setSearch] = useState("");
    const containerRef = useRef(null);

    //  Toggle selection
    const toggleOption = (item) => {
      if (value.includes(item)) {
        onChange(value.filter((v) => v !== item));
      } else {
        onChange([...value, item]);
      }
    };

    //  Filter options
    const filteredOptions = options.filter((opt) =>
      opt.toLowerCase().includes(search.toLowerCase())
    );

    //  Close dropdown ONLY on outside click
    useEffect(() => {
      const handleOutsideClick = (e) => {
        if (containerRef.current && !containerRef.current.contains(e.target)) {
          setOpen(false);
        }
      };

      document.addEventListener("mousedown", handleOutsideClick);
      return () =>
        document.removeEventListener("mousedown", handleOutsideClick);
    }, []);

    return (
      <div ref={containerRef} className="relative mb-6">
        <label className="block text-sm font-semibold mb-1">{label}</label>

        {/* INPUT */}
        <div
          className="border rounded-md p-3 flex justify-between items-center cursor-pointer bg-white"
          onClick={() => setOpen(true)}
        >
          <span className="text-sm text-gray-600">
            {value.length ? `${value.length} selected` : placeholder}
          </span>
          <span className="text-gray-400"></span>
        </div>

        {/* DROPDOWN */}
        {open && (
          <div className="absolute z-50 w-full bg-white border rounded-lg shadow-lg mt-2">
            {/* SEARCH */}
            <div className="p-2 border-b">
              <input
                type="text"
                className="w-full border rounded p-2 text-sm"
                placeholder="Search..."
                value={search}
                onChange={(e) => setSearch(e.target.value)}
              />
            </div>

            {/* OPTIONS */}
            <div className="max-h-60 overflow-y-auto p-2">
              {filteredOptions.length === 0 && (
                <p className="text-xs text-gray-400 p-2">No results found</p>
              )}

              {filteredOptions.map((opt) => (
                <div
                  key={opt}
                  className="flex items-center gap-2 p-2 rounded hover:bg-gray-100 cursor-pointer"
                  onClick={() => toggleOption(opt)}
                >
                  <input
                    type="checkbox"
                    checked={value.includes(opt)}
                    readOnly
                  />
                  <span className="text-sm">{opt}</span>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* SELECTED TAGS */}
        {value.length > 0 && (
          <div className="flex flex-wrap gap-2 mt-2">
            {value.map((v) => (
              <span
                key={v}
                className="bg-gray-100 border rounded-full px-3 py-1 text-xs flex items-center gap-2"
              >
                {v}
                <button
                  className="text-gray-500 hover:text-black"
                  onClick={() => toggleOption(v)}
                >
                  
                </button>
              </span>
            ))}
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="p-6 bg-white rounded-lg shadow max-w-auto mx-auto mt-8">
      <h2 className="text-2xl font-bold mb-4">Email Management</h2>

      {/* EMAIL TYPES TABLE */}
      <table className="w-full border mb-6">
        <thead>
          <tr className="border-b">
            <th className="p-3 text-left">Email Type</th>
            <th className="p-3 text-left">Description</th>
            <th className="p-3 text-left">Status</th>
          </tr>
        </thead>
        <tbody>
          {emailTypes.map((t) => (
            <tr
              key={t.id}
              className="border-b cursor-pointer hover:bg-gray-50"
              onClick={() => openStudyDrawer(t)}
            >
              <td className="p-3">{t.name}</td>
              <td>{t.description}</td>
              <td>{t.is_active ? "Enabled" : "Disabled"}</td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* SIDEBAR */}
      {drawerOpen && (
        <div className="fixed inset-0 bg-black/50 z-50">
          <div className="absolute right-0 top-0 h-full w-[1200px] bg-white p-6 overflow-auto">
            <div className="flex justify-between mb-4">
              <h3 className="font-bold">{selectedType?.name}</h3>
              <button onClick={() => setDrawerOpen(false)}>Close</button>
            </div>

            <select
              className="w-full border p-2 mb-2"
              onChange={(e) => {
                const userId = e.target.value;
                setSelectedUser(userId);
                setRules([]); //  CLEAR old rules
                fetchRules(userId); //  CORRECT function
              }}
            >
              <option>Select user</option>
              {users.map((u) => (
                <option key={u.id} value={u.id}>
                  {u.name}
                </option>
              ))}
            </select>

           

            <SearchableMultiSelect
              label="ClinicalTrials.gov Study Phase"
              options={phases}
              value={selectedPhases}
              placeholder="Select study phase(s)"
              onChange={(vals) => {
                setSelectedPhases(vals);
                setForm((prev) => ({ ...prev, phase: vals }));
              }}
            />

            <SearchableMultiSelect
              label="ClinicalTrials.gov Condition"
              options={conditions}
              value={selectedConditions}
              placeholder="Select condition(s)"
              onChange={(vals) => {
                setSelectedConditions(vals);
                setForm((prev) => ({ ...prev, condition_text: vals }));
              }}
            />

          

            <select
              className="w-full border p-2 mb-2"
              onChange={(e) =>
                setForm({ ...form, frequency_code: e.target.value })
              }
            >
              <option>Select frequency</option>
              {frequencies.map((f) => (
                <option key={f.code} value={f.code}>
                  {f.label}
                </option>
              ))}
            </select>

            {["DAILY", "WEEKLY", "MONTHLY"].includes(form.frequency_code) && (
              <input
                type="time"
                className="w-full border p-2 mb-2"
                onChange={(e) =>
                  setForm({ ...form, schedule_time: e.target.value })
                }
              />
            )}

            {form.frequency_code === "WEEKLY" && (
              <select
                className="w-full border p-2 mb-2"
                onChange={(e) =>
                  setForm({ ...form, schedule_day_of_week: e.target.value })
                }
              >
                <option value="">Select weekday</option>
                <option value="1">Monday</option>
                <option value="2">Tuesday</option>
                <option value="3">Wednesday</option>
                <option value="4">Thursday</option>
                <option value="5">Friday</option>
              </select>
            )}

            {form.frequency_code === "MONTHLY" && (
              <div className="mb-2">
                <label className="text-sm text-gray-600 block mb-1">
                  Select day of month
                </label>

                <input
                  type="date"
                  className="w-full border p-2 rounded"
                  onChange={(e) => {
                    if (!e.target.value) return;

                    const selectedDate = new Date(e.target.value);
                    const dayOfMonth = selectedDate.getDate(); //  ONLY DAY

                    setForm({
                      ...form,
                      schedule_day_of_month: dayOfMonth,
                    });
                  }}
                />

                {form.schedule_day_of_month && (
                  <p className="text-xs text-gray-500 mt-1">
                    Email will be sent on the{" "}
                    <b>{form.schedule_day_of_month}</b>
                    {getOrdinal(form.schedule_day_of_month)} of every month
                  </p>
                )}
              </div>
            )}

            <button
              className="w-full bg-black text-white p-2"
              onClick={saveRule}
            >
              {editingRule ? "Update Rule" : "Save Rule"}
            </button>

            {editingRule && (
              <button
                className="w-full mt-2 border p-2"
                onClick={() => {
                  setEditingRule(null);
                  setForm({
                    location_text: "",
                    condition_text: "",
                    frequency_code: "",
                  });
                  setSelectedPhases([]);
                  setSelectedConditions([]);
                }}
              >
                Cancel Edit
              </button>
            )}

            {/* RULES TABLE */}

            <div className="mt-4">
              {selectedUser &&
                rules.length > 0 &&
                rules.map((r) => {
                  const phases = parseJSON(r.phase);
                  const conditions = parseJSON(r.condition_text);

                  return (
                    <div
                      key={r.id}
                      className="border rounded-lg p-4 mb-4 bg-gray-50"
                    >
                     
                      {/* HEADER */}
                      <div className="flex justify-between items-start mb-2">
                        
                        
                        
                         {/* User Name */}
                      <div>
                        <h4 className="font-semibold text-lg">User</h4>
                        <p className="text-sm text-gray-500">
                          {users.find((u) => u.id === r.target_user_id)?.name}
                        </p>
                      </div>


                        <div>
                          <h4 className="font-semibold text-lg">
                            {r.rule_name}
                          </h4>
                          <p className="text-sm text-gray-500">
                            {formatFrequency(r)}
                          </p>
                        </div>

                        <div className="flex gap-2">
                          <button
                            className="px-3 py-1 text-sm border rounded hover:bg-gray-100"
                            onClick={() => {
                              setEditingRule(r);

                              setForm({
                                rule_name: r.rule_name,
                                location_text: r.location_text || "",
                                frequency_code: r.frequency_code,
                                schedule_day_of_week: r.schedule_day_of_week,
                                schedule_day_of_month: r.schedule_day_of_month,
                                schedule_time: r.schedule_time,
                                phase: parseJSON(r.phase),
                                condition_text: parseJSON(r.condition_text),
                              });

                              setSelectedPhases(parseJSON(r.phase));
                              setSelectedConditions(
                                parseJSON(r.condition_text)
                              );
                            }}
                          >
                            Edit
                          </button>

                          <button
                            className="px-3 py-1 text-sm border border-red-300 text-red-600 rounded hover:bg-red-50"
                            onClick={() => deleteRule(r.id)}
                          >
                            Delete
                          </button>
                        </div>
                      </div>

                      {/* PHASES */}
                      {phases.length > 0 && (
                        <div className="mb-2">
                          <p className="text-sm font-medium mb-1">
                            Study Phases
                          </p>
                          <div className="flex flex-wrap gap-2">
                            {phases.map((p) => (
                              <span
                                key={p}
                                className="bg-blue-100 text-blue-800 px-2 py-1 rounded text-xs"
                              >
                                {p}
                              </span>
                            ))}
                          </div>
                        </div>
                      )}

                      {/* CONDITIONS */}
                      {conditions.length > 0 && (
                        <div className="mb-2">
                          <p className="text-sm font-medium mb-1">Conditions</p>
                          <div className="flex flex-wrap gap-2">
                            {conditions.map((c) => (
                              <span
                                key={c}
                                className="bg-green-100 text-green-800 px-2 py-1 rounded text-xs"
                              >
                                {c}
                              </span>
                            ))}
                          </div>
                        </div>
                      )}

                      {/* STATUS */}
                      <div className="mt-2 text-sm text-gray-600">
                        Status:{" "}
                        <span
                          className={
                            r.is_enabled
                              ? "text-green-600 font-medium"
                              : "text-red-600"
                          }
                        >
                          {r.is_enabled ? "Enabled" : "Disabled"}
                        </span>
                      </div>
                    </div>
                  );
                })}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default EmailManagement;
