export class PerformanceMonitor {
  private static instance: PerformanceMonitor;
  private metrics: Map<string, number> = new Map();
  private loadingStates: Map<string, boolean> = new Map();

  private constructor() {}

  public static getInstance(): PerformanceMonitor {
    if (!PerformanceMonitor.instance) {
      PerformanceMonitor.instance = new PerformanceMonitor();
    }
    return PerformanceMonitor.instance;
  }

  // Start timing an operation
  public startTiming(operation: string): void {
    this.metrics.set(`${operation}_start`, performance.now());
    this.loadingStates.set(operation, true);
    console.log(` Started timing: ${operation}`);
  }

  // End timing an operation
  public endTiming(operation: string): number {
    const startTime = this.metrics.get(`${operation}_start`);
    if (!startTime) {
      console.warn(` No start time found for operation: ${operation}`);
      return 0;
    }

    const endTime = performance.now();
    const duration = endTime - startTime;
    
    this.metrics.set(`${operation}_duration`, duration);
    this.loadingStates.set(operation, false);
    
    console.log(` ${operation} completed in ${duration.toFixed(2)}ms`);
    
    // Warn about slow operations
    if (duration > 2000) {
      console.warn(` Slow operation detected: ${operation} took ${duration.toFixed(2)}ms`);
    }
    
    return duration;
  }

  // Get duration of an operation
  public getDuration(operation: string): number {
    return this.metrics.get(`${operation}_duration`) || 0;
  }

  // Check if operation is loading
  public isLoading(operation: string): boolean {
    return this.loadingStates.get(operation) || false;
  }

  // Get all metrics
  public getAllMetrics(): Record<string, number> {
    const result: Record<string, number> = {};
    this.metrics.forEach((value, key) => {
      if (key.endsWith('_duration')) {
        const operationName = key.replace('_duration', '');
        result[operationName] = value;
      }
    });
    return result;
  }

  // Get performance summary
  public getPerformanceSummary(): {
    totalOperations: number;
    averageLoadTime: number;
    slowOperations: Array<{ name: string; duration: number }>;
    fastOperations: Array<{ name: string; duration: number }>;
  } {
    const metrics = this.getAllMetrics();
    const operations = Object.entries(metrics);
    
    if (operations.length === 0) {
      return {
        totalOperations: 0,
        averageLoadTime: 0,
        slowOperations: [],
        fastOperations: []
      };
    }

    const totalTime = operations.reduce((sum, [, duration]) => sum + duration, 0);
    const averageLoadTime = totalTime / operations.length;

    const slowOperations = operations
      .filter(([, duration]) => duration > 1000)
      .map(([name, duration]) => ({ name, duration }))
      .sort((a, b) => b.duration - a.duration);

    const fastOperations = operations
      .filter(([, duration]) => duration <= 500)
      .map(([name, duration]) => ({ name, duration }))
      .sort((a, b) => a.duration - b.duration);

    return {
      totalOperations: operations.length,
      averageLoadTime,
      slowOperations,
      fastOperations
    };
  }

  // Monitor API call performance
  public monitorApiCall<T>(
    operation: string,
    apiCall: () => Promise<T>
  ): Promise<T> {
    this.startTiming(operation);
    
    return apiCall()
      .then((result) => {
        this.endTiming(operation);
        return result;
      })
      .catch((error) => {
        this.endTiming(operation);
        console.error(` API call failed: ${operation}`, error);
        throw error;
      });
  }

  // Monitor React Query performance
  public monitorQuery(queryKey: string[], duration: number): void {
    const operation = `query_${queryKey.join('_')}`;
    this.metrics.set(`${operation}_duration`, duration);
    
    if (duration > 1000) {
      console.warn(` Slow query detected: ${queryKey.join(', ')} took ${duration.toFixed(2)}ms`);
    }
  }

  // Clear all metrics
  public clearMetrics(): void {
    this.metrics.clear();
    this.loadingStates.clear();
    console.log(' Performance metrics cleared');
  }

  // Log performance report
  public logPerformanceReport(): void {
    const summary = this.getPerformanceSummary();
    
    console.group(' Performance Report');
    console.log(`Total Operations: ${summary.totalOperations}`);
    console.log(`Average Load Time: ${summary.averageLoadTime.toFixed(2)}ms`);
    
    if (summary.slowOperations.length > 0) {
      console.group(' Slow Operations (>1s)');
      summary.slowOperations.forEach(({ name, duration }) => {
        console.log(`${name}: ${duration.toFixed(2)}ms`);
      });
      console.groupEnd();
    }
    
    if (summary.fastOperations.length > 0) {
      console.group(' Fast Operations (<500ms)');
      summary.fastOperations.slice(0, 5).forEach(({ name, duration }) => {
        console.log(`${name}: ${duration.toFixed(2)}ms`);
      });
      console.groupEnd();
    }
    
    console.groupEnd();
  }

  // Setup automatic performance monitoring
  public setupAutoMonitoring(): void {
    // Log performance report every 5 minutes in development
    if (process.env.NODE_ENV === 'development') {
      setInterval(() => {
        this.logPerformanceReport();
      }, 5 * 60 * 1000);
    }

    // Monitor page load performance
    if (typeof window !== 'undefined') {
      window.addEventListener('load', () => {
        const loadTime = performance.now();
        console.log(` Page loaded in ${loadTime.toFixed(2)}ms`);
        this.metrics.set('page_load_duration', loadTime);
      });
    }
  }
}

// Export singleton instance
export const performanceMonitor = PerformanceMonitor.getInstance();
