import React, { createContext, useContext, useState, useEffect } from "react";
import { useAuth } from "./AuthContext";
import axiosInstance from "@/api/axios/axiosInstance";

interface Permission {
  id: number;
  name: string;
  display_name: string;
  description: string;
  category: string;
  resource: string;
  action: string;
  permission_source?: string;
  permission_name?: string;
}

interface AccessiblePage {
  id: number;
  page_path: string;
  page_name: string;
  page_category: string;
  required_permission: string | null;
  is_public: boolean;
  sort_order: number;
  icon: string | null;
  parent_page_id: number | null;
}

interface Role {
  id: number;
  name: string;
  display_name: string;
  description: string;
  is_system_role: boolean;
  is_active: boolean;
}

interface UserRole {
  id: number;
  name: string;
  display_name: string;
  description?: string;
}

interface PagePermission {
  page_id: number;
  page_path: string;
  page_name: string;
  page_category: string;
  icon: string | null;
  can_view: boolean;
  can_add: boolean;
  can_update: boolean;
  can_delete: boolean;
  sort_order: number;
}

interface PermissionContextType {
  permissions: string[];
  permissionDetails: Permission[];
  accessiblePages: AccessiblePage[];
  role: Role | null;
  roles: UserRole[];
  pagePermissions: PagePermission[];
  isSuperAdmin: boolean;
  loading: boolean;
  hasPermission: (permissionName: string) => boolean;
  hasAnyPermission: (permissionNames: string[]) => boolean;
  hasAllPermissions: (permissionNames: string[]) => boolean;
  hasRole: (roleName: string) => boolean;
  hasAnyRole: (roleNames: string[]) => boolean;
  canAccessPage: (pagePath: string) => boolean;
  canPerformAction: (pagePath: string, action: 'view' | 'add' | 'update' | 'delete') => boolean;
  refreshPermissions: () => Promise<void>;
}

const PermissionContext = createContext<PermissionContextType | undefined>(undefined);

export const PermissionProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const { user, isLoggedIn, token } = useAuth();
  const [permissions, setPermissions] = useState<string[]>([]);
  const [permissionDetails, setPermissionDetails] = useState<Permission[]>([]);
  const [accessiblePages, setAccessiblePages] = useState<AccessiblePage[]>([]);
  const [role, setRole] = useState<Role | null>(null);
  const [roles, setRoles] = useState<UserRole[]>([]);
  const [pagePermissions, setPagePermissions] = useState<PagePermission[]>([]);
  const [isSuperAdmin, setIsSuperAdmin] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(true);

  // Load user permissions and accessible pages
  const loadPermissions = async () => {
    if (!user || !token || !isLoggedIn) {
      setPermissions([]);
      setPermissionDetails([]);
      setAccessiblePages([]);
      setRole(null);
      setRoles([]);
      setPagePermissions([]);
      setIsSuperAdmin(false);
      setLoading(false);
      return;
    }

    try {
      setLoading(true);

      // Load user with roles
      const userWithRolesResponse = await axiosInstance.get(`/api/users/${user.id}/with-roles`);
      if (userWithRolesResponse.data.success) {
        const userData = userWithRolesResponse.data.data;
        setRoles(userData.roles || []);
      }

      // Load comprehensive user permissions (from all roles)
      const permissionsResponse = await axiosInstance.get(`/api/user-roles/user/${user.id}/permissions`);
      if (permissionsResponse.data.success) {
        const userPermissions = permissionsResponse.data.data;
        setPermissionDetails(userPermissions);
        setPermissions(userPermissions.map((p: Permission) => p.permission_name));
      }

      // Load page permissions
      const pagePermissionsResponse = await axiosInstance.get(`/api/role-page-permissions/user/${user.id}`);
      if (pagePermissionsResponse.data.success) {
        setPagePermissions(pagePermissionsResponse.data.data);
      }

      // Load accessible pages (for backward compatibility)
      const pagesResponse = await axiosInstance.get('/api/page-permissions/accessible/me');
      if (pagesResponse.data.success) {
        setAccessiblePages(pagesResponse.data.data);
      }



      // Load role information if user has role_id
      if (user.role_id) {
        try {
          const roleResponse = await axiosInstance.get(`/api/roles/${user.role_id}`);
          if (roleResponse.data) {
            setRole(roleResponse.data);
            // Check if super admin (by name or role ID 1)
            const isSuper = roleResponse.data.name === 'super_admin' || user.role_id === 1;
            setIsSuperAdmin(isSuper);

            console.log('Permission Context Debug:', {
              userId: user.id,
              roleId: user.role_id,
              roleName: roleResponse.data.name,
              isSuperAdmin: isSuper,
              roleData: roleResponse.data,
              checkByName: roleResponse.data.name === 'super_admin',
              checkById: user.role_id === 1
            });
          }
        } catch (roleError) {
          console.error('Error loading role:', roleError);
          // Fallback: if role ID is 1, assume super admin
          if (user.role_id === 1) {
            setIsSuperAdmin(true);
            console.log('Fallback: Setting super admin based on role_id = 1');
          }
        }
      }

    } catch (error) {
      console.error("Error loading permissions:", error);
      setPermissions([]);
      setPermissionDetails([]);
      setAccessiblePages([]);
      setRole(null);
      setIsSuperAdmin(false);
    } finally {
      setLoading(false);
    }
  };

  // Check if user has specific permission
  const hasPermission = (permissionName: string): boolean => {
    if (isSuperAdmin) return true;
    return permissions.includes(permissionName);
  };

  // Check if user has any of the specified permissions
  const hasAnyPermission = (permissionNames: string[]): boolean => {
    if (isSuperAdmin) return true;
    return permissionNames.some(permission => permissions.includes(permission));
  };

  // Check if user has all specified permissions
  const hasAllPermissions = (permissionNames: string[]): boolean => {
    if (isSuperAdmin) return true;
    return permissionNames.every(permission => permissions.includes(permission));
  };

  // Check if user has specific role
  const hasRole = (roleName: string): boolean => {
    if (isSuperAdmin) return true;
    return roles.some(role => role.name === roleName);
  };

  // Check if user has any of the specified roles
  const hasAnyRole = (roleNames: string[]): boolean => {
    if (isSuperAdmin) return true;
    return roleNames.some(roleName => roles.some(role => role.name === roleName));
  };

  // Check if user can perform specific action on a page
  const canPerformAction = (pagePath: string, action: 'view' | 'add' | 'update' | 'delete'): boolean => {
    if (isSuperAdmin) return true;

    const pagePermission = pagePermissions.find(p => p.page_path === pagePath);
    if (!pagePermission) return false;

    switch (action) {
      case 'view':
        return pagePermission.can_view;
      case 'add':
        return pagePermission.can_add;
      case 'update':
        return pagePermission.can_update;
      case 'delete':
        return pagePermission.can_delete;
      default:
        return false;
    }
  };

  // Check if user can access specific page
  const canAccessPage = (pagePath: string): boolean => {
    if (isSuperAdmin) return true;

    // First check page permissions (role-based access)
    const pagePermission = pagePermissions.find(p => p.page_path === pagePath);
    if (pagePermission && pagePermission.can_view) {
      return true;
    }

    // Fallback to accessible pages (permission-based access)
    return accessiblePages.some(page =>
      page.page_path === pagePath && (page.is_public || hasPermission(page.required_permission || ''))
    );
  };

  // Refresh permissions
  const refreshPermissions = async () => {
    await loadPermissions();
  };

  // Load permissions when user changes
  useEffect(() => {
    loadPermissions();
  }, [user, isLoggedIn, token]);

  const contextValue: PermissionContextType = {
    permissions,
    permissionDetails,
    accessiblePages,
    role,
    roles,
    pagePermissions,
    isSuperAdmin,
    loading,
    hasPermission,
    hasAnyPermission,
    hasAllPermissions,
    hasRole,
    hasAnyRole,
    canAccessPage,
    canPerformAction,
    refreshPermissions,
  };

  return (
    <PermissionContext.Provider value={contextValue}>
      {children}
    </PermissionContext.Provider>
  );
};

// Custom hook to use the PermissionContext
export const usePermissions = (): PermissionContextType => {
  const context = useContext(PermissionContext);
  if (!context) {
    throw new Error("usePermissions must be used within a PermissionProvider");
  }
  return context;
};

// HOC for permission-based rendering
export const withPermission = <P extends object>(
  Component: React.ComponentType<P>,
  requiredPermission: string,
  fallback?: React.ComponentType<P> | React.ReactNode
) => {
  return (props: P) => {
    const { hasPermission, loading } = usePermissions();

    if (loading) {
      return <div>Loading...</div>;
    }

    if (!hasPermission(requiredPermission)) {
      if (fallback) {
        if (React.isValidElement(fallback)) {
          return fallback;
        }
        const FallbackComponent = fallback as React.ComponentType<P>;
        return <FallbackComponent {...props} />;
      }
      return null;
    }

    return <Component {...props} />;
  };
};

// HOC for role-based rendering
export const withRole = <P extends object>(
  Component: React.ComponentType<P>,
  requiredRoles: string[],
  fallback?: React.ComponentType<P> | React.ReactNode
) => {
  return (props: P) => {
    const { role, loading } = usePermissions();

    if (loading) {
      return <div>Loading...</div>;
    }

    if (!role || !requiredRoles.includes(role.name)) {
      if (fallback) {
        if (React.isValidElement(fallback)) {
          return fallback;
        }
        const FallbackComponent = fallback as React.ComponentType<P>;
        return <FallbackComponent {...props} />;
      }
      return null;
    }

    return <Component {...props} />;
  };
};

// HOC for super admin only
export const withSuperAdmin = <P extends object>(
  Component: React.ComponentType<P>,
  fallback?: React.ComponentType<P> | React.ReactNode
) => {
  return (props: P) => {
    const { isSuperAdmin, loading } = usePermissions();

    if (loading) {
      return <div>Loading...</div>;
    }

    if (!isSuperAdmin) {
      if (fallback) {
        if (React.isValidElement(fallback)) {
          return fallback;
        }
        const FallbackComponent = fallback as React.ComponentType<P>;
        return <FallbackComponent {...props} />;
      }
      return null;
    }

    return <Component {...props} />;
  };
};
