import React, { useState, useEffect } from 'react';
import { toast } from 'sonner';
import secureLocalStorage from 'react-secure-storage';
import { useNavigate } from 'react-router-dom';
import { Button } from '../components/ui/button';
import { Input } from '../components/ui/input';

import { Badge } from '../components/ui/badge';
import {
  MagnifyingGlassIcon,

  EnvelopeIcon,
  FunnelIcon,
  TrashIcon,
  EyeIcon,
  Cog6ToothIcon,
  ChevronLeftIcon,
  ChevronRightIcon,
  EllipsisHorizontalIcon,
  DocumentDuplicateIcon,
  ArchiveBoxIcon,
  TagIcon,
  UserPlusIcon,

} from '@heroicons/react/24/outline';
import { Download, Upload, Phone, Plus, Workflow } from 'lucide-react';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '../components/ui/dropdown-menu';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from '../components/ui/dialog';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '../components/ui/select';
import { getAllContactLists, createList, addContactToList } from '../api/services/contact/contactService';

const Leads = () => {
  const navigate = useNavigate();
  const [leads, setLeads] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [pageSize, setPageSize] = useState(50);
  const [totalRecords, setTotalRecords] = useState(0);
  const [selectedLeads, setSelectedLeads] = useState([]);
  const [activeTab, setActiveTab] = useState('all');
  const [stats, setStats] = useState({
    total: 0,
    new_leads: 0,
    contacted: 0,
    interested: 0,
    not_interested: 0
  });
  const [isAddLeadOpen, setIsAddLeadOpen] = useState(false);
  const [newLead, setNewLead] = useState({
    business_name: '',
    phone: '',
    email: '',
    website: '',
    address: '',
    business_type: '',
    status: 'new'
  });
  const [showDeleteModal, setShowDeleteModal] = useState(false);

  // Add to Contact Modal States
  const [showAddToContactModal, setShowAddToContactModal] = useState(false);
  const [showCreateListModal, setShowCreateListModal] = useState(false);
  const [contactLists, setContactLists] = useState([]);
  const [listsLoading, setListsLoading] = useState(false);
  const [addToExistingList, setAddToExistingList] = useState(false);
  const [selectedListId, setSelectedListId] = useState('');
  const [addingToList, setAddingToList] = useState(false);
  const [newListData, setNewListData] = useState({
    list_name: '',
    type: 'General',
    list_description: ''
  });
  const [creatingList, setCreatingList] = useState(false);

  // Load leads from database
  const loadLeads = async () => {
    try {
      setLoading(true);
      const token = secureLocalStorage.getItem('token');
      if (!token) {
        toast.error('Please login to view leads');
        return;
      }

      const params = new URLSearchParams();
      if (statusFilter !== 'all') params.append('status', statusFilter);
      if (searchTerm.trim()) params.append('search', searchTerm.trim());

      // Add pagination parameters
      if (pageSize !== 'all') {
        params.append('page', currentPage.toString());
        params.append('limit', pageSize.toString());
      }

      const response = await fetch(`/api/leads-contacts?${params.toString()}`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      const result = await response.json();

      if (result.success) {
        setLeads(result.leads);
        // Update pagination info if provided by backend
        if (result.pagination) {
          setTotalPages(result.pagination.totalPages || 1);
          setTotalRecords(result.pagination.totalRecords || result.leads.length);
        } else {
          // Fallback for when pagination is not implemented in backend
          setTotalRecords(result.leads.length);
          setTotalPages(1);
        }
      } else {
        toast.error(result.message || 'Failed to load leads');
      }
    } catch (error) {
      console.error('Load leads error:', error);
      toast.error('Failed to load leads');
    } finally {
      setLoading(false);
    }
  };

  // Load lead statistics
  const loadStats = async () => {
    try {
      const token = secureLocalStorage.getItem('token');
      if (!token) return;

      const response = await fetch('/api/leads-contacts/stats', {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      const result = await response.json();

      if (result.success) {
        setStats(result.stats);
      }
    } catch (error) {
      console.error('Load stats error:', error);
    }
  };

  // Update lead status
  const updateLeadStatus = async (leadId, status) => {
    try {
      const token = secureLocalStorage.getItem('token');
      if (!token) return;

      const response = await fetch(`/api/leads-contacts/${leadId}/status`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ status })
      });

      const result = await response.json();

      if (result.success) {
        toast.success('Lead status updated successfully');
        loadLeads();
        loadStats();
      } else {
        toast.error(result.message || 'Failed to update lead status');
      }
    } catch (error) {
      console.error('Update lead status error:', error);
      toast.error('Failed to update lead status');
    }
  };

  // Delete lead
  const deleteLead = async (leadId) => {
    if (!window.confirm('Are you sure you want to delete this lead?')) {
      return;
    }

    try {
      const token = secureLocalStorage.getItem('token');
      if (!token) return;

      const response = await fetch(`/api/leads-contacts/${leadId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      const result = await response.json();

      if (result.success) {
        toast.success('Lead deleted successfully');
        loadLeads();
        loadStats();
      } else {
        toast.error(result.message || 'Failed to delete lead');
      }
    } catch (error) {
      console.error('Delete lead error:', error);
      toast.error('Failed to delete lead');
    }
  };

  // Add new lead functions
  const handleAddLead = async () => {
    try {
      // Validate required fields
      if (!newLead.business_name.trim()) {
        toast.error('Business name is required');
        return;
      }

      const token = secureLocalStorage.getItem('token');
      if (!token) {
        toast.error('Authentication required');
        return;
      }

      const response = await fetch(`${import.meta.env.VITE_APP_BASE_URL}/api/leads-contacts/save`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          ...newLead,
          // Add required fields for backend
          latitude: null,
          longitude: null,
          place_id: `manual_${Date.now()}`, // Generate unique place_id for manual entries
          search_query: 'Manual Entry',
          search_location: 'Manual Entry',
          rating: null,
          total_ratings: null
        })
      });

      const result = await response.json();

      if (result.success) {
        toast.success('Lead added successfully');
        setIsAddLeadOpen(false);
        setNewLead({
          business_name: '',
          phone: '',
          email: '',
          website: '',
          address: '',
          business_type: '',
          status: 'new'
        });
        loadLeads();
        loadStats();
      } else {
        toast.error(result.message || 'Failed to add lead');
      }
    } catch (error) {
      console.error('Add lead error:', error);
      toast.error('Failed to add lead');
    }
  };

  const handleInputChange = (field, value) => {
    setNewLead(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleCloseSidebar = () => {
    setIsAddLeadOpen(false);
    setNewLead({
      business_name: '',
      phone: '',
      email: '',
      website: '',
      address: '',
      business_type: '',
      status: 'new'
    });
  };

  // Get status badge color
  const getStatusBadgeColor = (status) => {
    switch (status) {
      case 'new': return 'bg-blue-100 text-blue-800';
      case 'contacted': return 'bg-yellow-100 text-yellow-800';
      case 'interested': return 'bg-green-100 text-green-800';
      case 'not_interested': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  // Format date for GHL style
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const now = new Date();
    const diffInHours = Math.floor((now - date) / (1000 * 60 * 60));

    if (diffInHours < 1) {
      return 'Just now';
    } else if (diffInHours < 24) {
      return `${diffInHours} hours ago`;
    } else {
      return date.toLocaleDateString('en-US', {
        month: 'short',
        day: '2-digit',
        year: 'numeric'
      });
    }
  };

  // Format time for GHL style
  const formatTime = (dateString) => {
    return new Date(dateString).toLocaleTimeString('en-US', {
      hour: '2-digit',
      minute: '2-digit',
      hour12: true
    });
  };

  // Generate avatar initials
  const getInitials = (name) => {
    if (!name) return 'UN';
    const words = name.split(' ');
    if (words.length >= 2) {
      return (words[0][0] + words[1][0]).toUpperCase();
    }
    return name.substring(0, 2).toUpperCase();
  };

  // Generate avatar color based on name
  const getAvatarColor = (name) => {
    if (!name) return 'bg-gray-500';
    const colors = [
      'bg-blue-500', 'bg-green-500', 'bg-yellow-500', 'bg-red-500',
      'bg-purple-500', 'bg-pink-500', 'bg-indigo-500', 'bg-teal-500'
    ];
    const index = name.charCodeAt(0) % colors.length;
    return colors[index];
  };

  // Toggle lead selection
  const toggleLeadSelection = (leadId) => {
    setSelectedLeads(prev =>
      prev.includes(leadId)
        ? prev.filter(id => id !== leadId)
        : [...prev, leadId]
    );
  };

  // Select all leads
  const toggleSelectAll = () => {
    if (selectedLeads.length === leads.length) {
      setSelectedLeads([]);
    } else {
      setSelectedLeads(leads.map(lead => lead.id));
    }
  };

  // Handle lead click to navigate to detail page
  const handleLeadClick = (leadId) => {
    navigate(`/leads/${leadId}`);
  };

  // Handle delete selected leads
  const handleDeleteSelectedLeads = () => {
    if (selectedLeads.length === 0) {
      toast.error('Please select leads to delete');
      return;
    }

    // Show confirmation modal
    setShowDeleteModal(true);
  };

  // Confirm delete selected leads
  const confirmDeleteSelectedLeads = async () => {
    setShowDeleteModal(false);

    try {
      const token = secureLocalStorage.getItem('token');
      if (!token) {
        toast.error('Authentication required');
        return;
      }

      // Delete leads one by one using the existing API endpoint
      const deletePromises = selectedLeads.map(leadId =>
        fetch(`${import.meta.env.VITE_APP_BASE_URL}/api/leads-contacts/${leadId}`, {
          method: 'DELETE',
          headers: {
            'Authorization': `Bearer ${token}`
          }
        })
      );

      const responses = await Promise.all(deletePromises);
      const results = await Promise.all(responses.map(res => res.json()));

      // Check if all deletions were successful
      const successCount = results.filter(result => result.success).length;
      const failCount = results.length - successCount;

      if (successCount > 0) {
        toast.success(`Successfully deleted ${successCount} lead(s)`);
        // Clear selection
        setSelectedLeads([]);
        // Refresh the leads list and stats
        loadLeads();
        loadStats();
      }

      if (failCount > 0) {
        toast.error(`Failed to delete ${failCount} lead(s)`);
      }

    } catch (error) {
      console.error('Delete leads error:', error);
      toast.error('Failed to delete selected leads');
    }
  };

  // Handle export selected leads to CSV
  const handleExportSelectedLeads = () => {
    if (selectedLeads.length === 0) {
      toast.error('Please select leads to export');
      return;
    }

    try {
      // Get selected leads data
      const selectedLeadsData = leads.filter(lead => selectedLeads.includes(lead.id));

      // Define CSV headers
      const headers = [
        'Business Name',
        'Phone',
        'Email',
        'Website',
        'Address',
        'Business Type',
        'Status',
        'Rating',
        'Total Ratings',
        'Created Date'
      ];

      // Convert leads data to CSV format
      const csvData = selectedLeadsData.map(lead => [
        lead.business_name || '',
        lead.phone || '',
        lead.email || '',
        lead.website || '',
        lead.address || '',
        lead.business_type || '',
        lead.status || '',
        lead.rating || '',
        lead.total_ratings || '',
        lead.created_at ? new Date(lead.created_at).toLocaleDateString() : ''
      ]);

      // Combine headers and data
      const csvContent = [headers, ...csvData]
        .map(row => row.map(field => `"${field}"`).join(','))
        .join('\n');

      // Create and download CSV file
      const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
      const link = document.createElement('a');
      const url = URL.createObjectURL(blob);

      link.setAttribute('href', url);
      link.setAttribute('download', `leads_export_${new Date().toISOString().split('T')[0]}.csv`);
      link.style.visibility = 'hidden';

      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);

      toast.success(`Successfully exported ${selectedLeads.length} lead(s) to CSV`);

    } catch (error) {
      console.error('Export leads error:', error);
      toast.error('Failed to export leads');
    }
  };

  // Handle page size change
  const handlePageSizeChange = (newPageSize) => {
    setPageSize(newPageSize);
    setCurrentPage(1); // Reset to first page when changing page size
  };

  // Handle page change
  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
  };

  // Load contact lists for Add to Contact modal
  const loadContactLists = async () => {
    try {
      setListsLoading(true);
      const result = await getAllContactLists();
      if (result.success && result.data) {
        setContactLists(result.data);
      }
    } catch (error) {
      console.error('Load contact lists error:', error);
    } finally {
      setListsLoading(false);
    }
  };

  // Handle Add to Contact button click
  const handleAddToContact = () => {
    if (selectedLeads.length === 0) {
      toast.error('Please select at least one lead');
      return;
    }
    loadContactLists();
    setShowAddToContactModal(true);
  };

  // Handle Create New List
  const handleCreateNewList = async () => {
    if (!newListData.list_name.trim()) {
      toast.error('List name is required');
      return;
    }
    if (!newListData.list_description.trim()) {
      toast.error('List description is required');
      return;
    }

    try {
      setCreatingList(true);
      const result = await createList({
        listName: newListData.list_name,
        description: newListData.list_description,
        type: newListData.type
      });

      if (result.success) {
        // Refresh the lists
        await loadContactLists();
        // Close create list modal
        setShowCreateListModal(false);
        // Reset form
        setNewListData({
          list_name: '',
          type: 'General',
          list_description: ''
        });
      }
    } catch (error) {
      console.error('Create list error:', error);
    } finally {
      setCreatingList(false);
    }
  };

  // Handle Add to Existing List
  const handleAddToExistingList = async () => {
    if (!selectedListId) {
      toast.error('Please select a list');
      return;
    }

    try {
      setAddingToList(true);
      const selectedLeadsData = leads.filter(lead => selectedLeads.includes(lead.id));

      // Add each selected lead to the list
      for (const lead of selectedLeadsData) {
        await addContactToList(selectedListId, {
          email: lead.email || `${lead.business_name?.toLowerCase().replace(/\s+/g, '')}@placeholder.com`,
          contact_number: lead.phone || '',
          phoneNumber: lead.phone || '',
          fullName: lead.business_name || ''
        });
      }

      toast.success(`Successfully added ${selectedLeads.length} lead(s) to the list`);

      // Close modal and reset states
      setShowAddToContactModal(false);
      setAddToExistingList(false);
      setSelectedListId('');
      setSelectedLeads([]);
    } catch (error) {
      console.error('Add to list error:', error);
    } finally {
      setAddingToList(false);
    }
  };

  // Close Add to Contact Modal
  const closeAddToContactModal = () => {
    setShowAddToContactModal(false);
    setAddToExistingList(false);
    setSelectedListId('');
  };

  // Close Create List Modal
  const closeCreateListModal = () => {
    setShowCreateListModal(false);
    setNewListData({
      list_name: '',
      type: 'General',
      list_description: ''
    });
  };

  useEffect(() => {
    loadLeads();
    loadStats();
  }, [statusFilter, searchTerm, currentPage, pageSize]);

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header with Tabs */}
      <div className="bg-white border-b border-gray-200">
        <div className="px-6 py-4">
          <div className="flex items-center  mb-6 gap-6">
            <h1 className="text-2xl font-semibold text-gray-900">Leads</h1>
            <div className="flex items-center gap-2 ">
              <Button size="sm" variant="outline">
                <Plus className="h-4 w-4 mr-2" />
                Create
              </Button>
              <Button size="sm" variant="outline">
                Bulk Actions
              </Button>
              <Button size="sm" variant="outline">
                Restore
              </Button>
              <Button size="sm" variant="outline">
                Tasks
              </Button>
              <Button size="sm" variant="outline">
                Companies
              </Button>
              <Button size="sm" variant="outline">
                Manage Smart Lists
              </Button>
              <Button size="sm" variant="ghost">
                <Cog6ToothIcon className="h-4 w-4 stroke-[2]" />
              </Button>
            </div>
          </div>

          {/* Tab Navigation */}
          <div className="flex items-center gap-6 border-b border-gray-200">
            <button
              onClick={() => setActiveTab('all')}
              className={`pb-3 px-1 text-sm font-medium border-b-2 transition-colors ${
                activeTab === 'all'
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700'
              }`}
            >
              All
            </button>
            <button
              onClick={() => setActiveTab('new')}
              className={`pb-3 px-1 text-sm font-medium border-b-2 transition-colors ${
                activeTab === 'new'
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700'
              }`}
            >
              New
            </button>
            <button
              onClick={() => setActiveTab('contacted')}
              className={`pb-3 px-1 text-sm font-medium border-b-2 transition-colors ${
                activeTab === 'contacted'
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700'
              }`}
            >
              Contacted
            </button>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="px-6 py-4">
        {/* Toolbar */}
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-2">
            <Button
              size="sm"
              variant="outline"
              title="Add Contact"
              onClick={() => setIsAddLeadOpen(true)}
            >
              <Plus className="h-4 w-4" />
            </Button>
            <Button size="sm" variant="outline">
              <FunnelIcon className="h-4 w-4 stroke-[2] " />
            </Button>
            <Button size="sm" variant="outline" title="please select lead to add automation ">
              <Workflow className="h-4 w-4" />
            </Button>
            <Button size="sm" variant="outline">
              <EnvelopeIcon className="h-4 w-4 stroke-[2]" />
            </Button>
            <Button size="sm" variant="outline">
              <Phone className="h-4 w-4" />
            </Button>
            <Button size="sm" variant="outline" title="delete leads" onClick={handleDeleteSelectedLeads}>
              <TrashIcon className="h-4 w-4 stroke-[2]" />
            </Button>
            <Button size="sm" variant="outline">
              <EyeIcon className="h-4 w-4 stroke-[2]" />
            </Button>
            <Button size="sm" variant="outline">
              <TagIcon className="h-4 w-4 stroke-[2]" />
            </Button>
            <Button size="sm" variant="outline">
              <ArchiveBoxIcon className="h-4 w-4 stroke-[2]" />
            </Button>
            <Button size="sm" variant="outline">
              <DocumentDuplicateIcon className="h-4 w-4 stroke-[2]" />
            </Button>
            <Button size="sm" variant="outline" title="Export selected leads" onClick={handleExportSelectedLeads}>
              <Download className="h-4 w-4 stroke-[2]" />
            </Button>
            <Button size="sm" variant="outline" title="Import leads from CSV" onClick={() => navigate('/leads-contacts/import')}>
              <Upload className="h-4 w-4 stroke-[2]" />
            </Button>
            <DropdownMenu modal={false}>
              <DropdownMenuTrigger asChild>
                <Button size="sm" variant="outline">
                  <EllipsisHorizontalIcon className="h-4 w-4 stroke-[2]" />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end">
                <DropdownMenuItem
                  onClick={handleAddToContact}
                  disabled={selectedLeads.length === 0}
                  className={selectedLeads.length === 0 ? 'opacity-50 cursor-not-allowed' : 'cursor-pointer'}
                >
                  <UserPlusIcon className="h-4 w-4 mr-2 stroke-[2]" />
                  Add to contact
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>

          <div className="flex items-center gap-4">
            {/* <Button size="sm" variant="outline">
              Columns
            </Button> */}
            <div className="relative">
              <MagnifyingGlassIcon className="h-4 w-4 absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
              <Input
                placeholder="Quick search"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10 w-64 h-8"
              />
            </div>
            <Button size="sm" variant="outline">
              More Filters
            </Button>
          </div>
        </div>

        {/* Table */}
        <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
          {/* Table Header */}
          <div className="bg-gray-50 border-b border-gray-200 px-4 py-3">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-2">
                <input
                  type="checkbox"
                  checked={selectedLeads.length === leads.length && leads.length > 0}
                  onChange={toggleSelectAll}
                  className="rounded border-gray-300"
                />
                <span className="text-sm text-gray-600">
                  Total <strong>{totalRecords || stats.total}</strong> records.
                  {pageSize !== 'all' && (
                    <span> Page {currentPage} of {totalPages}</span>
                  )}
                </span>
              </div>
              <div className="flex items-center gap-4">
                {/* Page Size Selector */}
                <div className="flex items-center gap-2">
                  <span className="text-sm text-gray-600">Show:</span>
                  <select
                    value={pageSize}
                    onChange={(e) => handlePageSizeChange(e.target.value === 'all' ? 'all' : parseInt(e.target.value))}
                    className="text-sm border border-gray-300 rounded px-2 py-1 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  >
                    <option value={25}>25</option>
                    <option value={50}>50</option>
                    <option value={100}>100</option>
                    <option value="all">All</option>
                  </select>
                  <span className="text-sm text-gray-600">per page</span>
                </div>

                {/* Pagination Controls */}
                {pageSize !== 'all' && totalPages > 1 && (
                  <div className="flex items-center gap-1">
                    <Button
                      size="sm"
                      variant="ghost"
                      disabled={currentPage === 1}
                      onClick={() => handlePageChange(currentPage - 1)}
                    >
                      <ChevronLeftIcon className="h-4 w-4" />
                    </Button>

                    {/* Page Numbers */}
                    <div className="flex items-center gap-1">
                      {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                        let pageNum;
                        if (totalPages <= 5) {
                          pageNum = i + 1;
                        } else if (currentPage <= 3) {
                          pageNum = i + 1;
                        } else if (currentPage >= totalPages - 2) {
                          pageNum = totalPages - 4 + i;
                        } else {
                          pageNum = currentPage - 2 + i;
                        }

                        return (
                          <Button
                            key={pageNum}
                            size="sm"
                            variant={currentPage === pageNum ? "default" : "ghost"}
                            onClick={() => handlePageChange(pageNum)}
                            className="w-8 h-8 p-0"
                          >
                            {pageNum}
                          </Button>
                        );
                      })}
                    </div>

                    <Button
                      size="sm"
                      variant="ghost"
                      disabled={currentPage === totalPages}
                      onClick={() => handlePageChange(currentPage + 1)}
                    >
                      <ChevronRightIcon className="h-4 w-4" />
                    </Button>
                  </div>
                )}
              </div>
            </div>
          </div>

          {/* Table Content */}
          <div className="divide-y divide-gray-200">
            {/* Column Headers */}
            <div className="bg-white px-4 py-3 grid grid-cols-12 gap-4 text-sm font-medium text-gray-500 uppercase tracking-wider">
              <div className="col-span-1"></div>
              <div className="col-span-3">Name</div>
              <div className="col-span-2">Phone</div>
              <div className="col-span-2">Email</div>
              <div className="col-span-1">Tags</div>
              <div className="col-span-2">Created</div>
              <div className="col-span-1">Last Activity</div>
              
            </div>

            {/* Table Rows */}
            {loading ? (
              <div className="px-4 py-8 text-center">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto"></div>
                <p className="mt-2 text-gray-600">Loading contacts...</p>
              </div>
            ) : leads.length === 0 ? (
              <div className="px-4 py-8 text-center text-gray-500">
                <MagnifyingGlassIcon className="h-12 w-12 mx-auto mb-4 text-gray-300" />
                <p>No contacts found</p>
                <p className="text-sm">Try adjusting your search or filters</p>
              </div>
            ) : (
              leads.map((lead) => (
                <div
                  key={lead.id}
                  className="bg-white px-4 py-3 grid grid-cols-12 gap-4 hover:bg-gray-50 transition-colors cursor-pointer"
                  
                >
                  {/* Checkbox */}
                  <div className="col-span-1 flex items-center">
                    <input
                      type="checkbox"
                      checked={selectedLeads.includes(lead.id)}
                      onChange={() => toggleLeadSelection(lead.id)}
                      className="rounded border-gray-300"
                    />
                  </div>

                  {/* Name with Avatar */}
                  <div className="col-span-3 flex items-center gap-3" onClick={() => handleLeadClick(lead.id)}>
                    <div className={`w-8 h-8 rounded-full flex items-center justify-center text-white text-xs font-medium ${getAvatarColor(lead.business_name)}`}>
                      {getInitials(lead.business_name)}
                    </div>
                    <div>
                      <div className="font-medium text-gray-900">{lead.business_name}</div>
                      {lead.business_type && (
                        <div className="text-xs text-gray-500">{lead.business_type}</div>
                      )}
                    </div>
                  </div>

                  {/* Phone */}
                  <div className="col-span-2 flex items-center" onClick={() => handleLeadClick(lead.id)}>
                    {lead.phone ? (
                      <div className="flex items-center gap-2">
                        <Phone className="h-4 w-4 text-green-500" />
                        <span className="text-sm text-gray-900">{lead.phone}</span>
                      </div>
                    ) : (
                      <span className="text-sm text-gray-400">-</span>
                    )}
                  </div>

                  {/* Email */}
                  <div className="col-span-2 flex items-center" onClick={() => handleLeadClick(lead.id)}>
                    {lead.email ? (
                      <div className="flex items-center gap-2">
                        <EnvelopeIcon className="h-4 w-4 text-blue-500" />
                        <span className="text-sm text-gray-900">{lead.email}</span>
                      </div>
                    ) : (
                      <span className="text-sm text-gray-400">-</span>
                    )}
                  </div>

                  {/* Tags */}
                  <div className="col-span-1 flex items-center">
                    {lead.tags && lead.tags.length > 0 ? (
                      <div className="flex flex-wrap gap-1">
                        {lead.tags.slice(0, 2).map((tag) => (
                          <Badge
                            key={tag.tag_id}
                            variant="secondary"
                            className={`text-xs ${getStatusBadgeColor(lead.status)}`}
                          >
                            {tag.tag_name.replace('_', ' ')}
                          </Badge>
                        ))}
                        {lead.tags.length > 2 && (
                          <Badge
                            variant="outline"
                            className="text-xs"
                          >
                            +{lead.tags.length - 2}
                          </Badge>
                        )}
                      </div>
                    ) : (
                      <span className="text-xs text-gray-400">No tags</span>
                    )}
                  </div>

                  {/* Created */}
                  <div className="col-span-2 flex items-center">
                    <div className="text-sm">
                      <div className="text-gray-900">{formatDate(lead.created_at)}</div>
                      <div className="text-xs text-gray-500">{formatTime(lead.created_at)}</div>
                    </div>
                  </div>

                  {/* Last Activity */}
                  <div className="col-span-1 flex items-center">
                    <div className="flex items-center gap-1">
                      <div className="w-2 h-2 bg-green-400 rounded-full"></div>
                      <span className="text-sm text-gray-900">{formatDate(lead.updated_at || lead.created_at)}</span>
                    </div>
                  </div>

                  
                </div>
              ))
            )}
          </div>
        </div>
      </div>

      {/* Add Lead Sidebar */}
      {isAddLeadOpen && (
        <div className="fixed inset-0 z-50 overflow-hidden">
          <div className="absolute inset-0 bg-black bg-opacity-50" onClick={handleCloseSidebar}></div>
          <div className="absolute right-0 top-0 h-full w-96 bg-white shadow-xl">
            <div className="flex flex-col h-full">
              {/* Header */}
              <div className="px-6 py-4 border-b border-gray-200">
                <div className="flex items-center justify-between">
                  <h2 className="text-lg font-semibold text-gray-900">Add New Lead</h2>
                  <button
                    onClick={handleCloseSidebar}
                    className="text-gray-400 hover:text-gray-600"
                  >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>
              </div>

              {/* Form */}
              <div className="flex-1 overflow-y-auto px-6 py-4">
                <div className="space-y-4">
                  {/* Business Name - Required */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Business Name <span className="text-red-500">*</span>
                    </label>
                    <Input
                      value={newLead.business_name}
                      onChange={(e) => handleInputChange('business_name', e.target.value)}
                      placeholder="Enter business name"
                      className="w-full"
                    />
                  </div>

                  {/* Phone */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Phone</label>
                    <Input
                      value={newLead.phone}
                      onChange={(e) => handleInputChange('phone', e.target.value)}
                      placeholder="Enter phone number"
                      className="w-full"
                    />
                  </div>

                  {/* Email */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Email</label>
                    <Input
                      value={newLead.email}
                      onChange={(e) => handleInputChange('email', e.target.value)}
                      placeholder="Enter email address"
                      type="email"
                      className="w-full"
                    />
                  </div>

                  {/* Website */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Website</label>
                    <Input
                      value={newLead.website}
                      onChange={(e) => handleInputChange('website', e.target.value)}
                      placeholder="Enter website URL"
                      className="w-full"
                    />
                  </div>

                  {/* Address */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Address</label>
                    <Input
                      value={newLead.address}
                      onChange={(e) => handleInputChange('address', e.target.value)}
                      placeholder="Enter business address"
                      className="w-full"
                    />
                  </div>

                  {/* Business Type */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Business Type</label>
                    <Input
                      value={newLead.business_type}
                      onChange={(e) => handleInputChange('business_type', e.target.value)}
                      placeholder="Enter business type"
                      className="w-full"
                    />
                  </div>

                  {/* Status */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Status</label>
                    <select
                      value={newLead.status}
                      onChange={(e) => handleInputChange('status', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      <option value="new">New</option>
                      <option value="contacted">Contacted</option>
                      <option value="interested">Interested</option>
                      <option value="not_interested">Not Interested</option>
                    </select>
                  </div>
                </div>
              </div>

              {/* Footer */}
              <div className="px-6 py-4 border-t border-gray-200">
                <div className="flex space-x-3">
                  <Button
                    variant="outline"
                    onClick={handleCloseSidebar}
                    className="flex-1"
                  >
                    Cancel
                  </Button>
                  <Button
                    onClick={handleAddLead}
                    className="flex-1 bg-blue-600 hover:bg-blue-700"
                  >
                    Add Lead
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Delete Confirmation Modal */}
      {showDeleteModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 w-full max-w-md mx-4">
            <div className="flex items-center gap-3 mb-4">
              <div className="w-10 h-10 bg-red-100 rounded-full flex items-center justify-center">
                <TrashIcon className="h-5 w-5 text-red-600" />
              </div>
              <div>
                <h3 className="text-lg font-semibold text-gray-900">Delete Leads</h3>
                <p className="text-sm text-gray-500">This action cannot be undone</p>
              </div>
            </div>

            <div className="mb-6">
              <p className="text-gray-700">
                Are you sure you want to delete <span className="font-semibold">{selectedLeads.length}</span> selected lead(s)?
                This will permanently remove them from your database.
              </p>
            </div>

            <div className="flex gap-3">
              <Button
                variant="outline"
                onClick={() => setShowDeleteModal(false)}
                className="flex-1"
              >
                Cancel
              </Button>
              <Button
                onClick={confirmDeleteSelectedLeads}
                className="flex-1 bg-red-600 hover:bg-red-700 text-white"
              >
                Delete {selectedLeads.length} Lead{selectedLeads.length > 1 ? 's' : ''}
              </Button>
            </div>
          </div>
        </div>
      )}

      {/* Add to Contact Modal */}
      <Dialog open={showAddToContactModal} onOpenChange={setShowAddToContactModal}>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle>Add to Contact List</DialogTitle>
          </DialogHeader>

          <div className="py-4">
            {/* Selected Leads Display */}
            <div className="mb-4">
              <p className="text-sm text-gray-500 mb-2">Selected Leads ({selectedLeads.length})</p>
              <div className="flex flex-wrap gap-2 max-h-32 overflow-y-auto">
                {leads.filter(lead => selectedLeads.includes(lead.id)).map((lead) => (
                  <div key={lead.id} className="flex items-center gap-2 bg-gray-100 rounded-full px-3 py-1">
                    <div className={`w-6 h-6 rounded-full flex items-center justify-center text-white text-xs font-medium ${getAvatarColor(lead.business_name)}`}>
                      {getInitials(lead.business_name)}
                    </div>
                    <span className="text-sm text-gray-700">{lead.business_name}</span>
                  </div>
                ))}
              </div>
            </div>

            {/* Create New List Button */}
            <Button
              variant="outline"
              className="w-full mb-4"
              onClick={() => setShowCreateListModal(true)}
            >
              <Plus className="h-4 w-4 mr-2" />
              Create New List
            </Button>

            {/* Add to Existing List Checkbox */}
            <div className="flex items-center space-x-2 mb-4">
              <input
                type="checkbox"
                id="addToExisting"
                checked={addToExistingList}
                onChange={(e) => setAddToExistingList(e.target.checked)}
                className="rounded border-gray-300"
              />
              <label htmlFor="addToExisting" className="text-sm text-gray-700 cursor-pointer">
                Add to Existing List
              </label>
            </div>

            {/* Existing Lists Dropdown */}
            {addToExistingList && (
              <div className="mb-4">
                {listsLoading ? (
                  <div className="text-center py-2">
                    <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600 mx-auto"></div>
                  </div>
                ) : contactLists.length === 0 ? (
                  <p className="text-sm text-gray-500 text-center py-2">No lists available. Create one first.</p>
                ) : (
                  <Select value={selectedListId} onValueChange={setSelectedListId}>
                    <SelectTrigger className="w-full">
                      <SelectValue placeholder="Select a list" />
                    </SelectTrigger>
                    <SelectContent>
                      {contactLists.map((list) => (
                        <SelectItem key={list.id} value={String(list.id)}>
                          {list.listName || list.list_name} ({list.contacts_count || 0} contacts)
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                )}
              </div>
            )}
          </div>

          <DialogFooter>
            <Button variant="outline" onClick={closeAddToContactModal}>
              Cancel
            </Button>
            {addToExistingList && selectedListId && (
              <Button
                onClick={handleAddToExistingList}
                disabled={addingToList}
                className="bg-blue-600 hover:bg-blue-700"
              >
                {addingToList ? 'Adding...' : 'Save'}
              </Button>
            )}
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Create New List Modal */}
      <Dialog open={showCreateListModal} onOpenChange={setShowCreateListModal}>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle>Create New List</DialogTitle>
          </DialogHeader>

          <div className="py-4 space-y-4">
            {/* List Name */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                List Name <span className="text-red-500">*</span>
              </label>
              <Input
                value={newListData.list_name}
                onChange={(e) => setNewListData(prev => ({ ...prev, list_name: e.target.value }))}
                placeholder="Enter list name"
                className="w-full"
              />
            </div>

            {/* Type */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Type
              </label>
              <Select
                value={newListData.type}
                onValueChange={(value) => setNewListData(prev => ({ ...prev, type: value }))}
              >
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="Select type" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="Marketing">Marketing</SelectItem>
                  <SelectItem value="Sales">Sales</SelectItem>
                  <SelectItem value="Event">Event</SelectItem>
                  <SelectItem value="Customer">Customer</SelectItem>
                  <SelectItem value="General">General</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {/* List Description */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                List Description <span className="text-red-500">*</span>
              </label>
              <textarea
                value={newListData.list_description}
                onChange={(e) => setNewListData(prev => ({ ...prev, list_description: e.target.value }))}
                placeholder="Enter list description"
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent min-h-[80px]"
              />
            </div>
          </div>

          <DialogFooter>
            <Button variant="outline" onClick={closeCreateListModal}>
              Cancel
            </Button>
            <Button
              onClick={handleCreateNewList}
              disabled={creatingList}
              className="bg-blue-600 hover:bg-blue-700"
            >
              {creatingList ? 'Creating...' : 'Create List'}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>


    </div>
  );
};

export default Leads;
